ентирован на общечеловеческие нравственные ценности, иметь прочные убеждения, быть терпеливым и настойчивым. Эти качества присущи «гуманисту», выполняющему функции руководителя. Человек такого типа настроен на решение стратегических задач. Решение производственных вопросов у него неразрывно связано с налаживанием и сохранением положительных взаимоотношений в коллективе. Глубоко разбирается в эмоциональных связях, хорошо видит личностные качества людей, на этой основе безошибочно прогнозирует состояние психологического климата в коллективе. Очень ценит благоприятную эмоциональную атмосферу, в которой может продуктивно работать. Не склонен к лидерству. Но в определенных случаях может взять на себя функции фронтального лидера. Совершенно не способен устанавливать иерархическую структуру. Отношение к человеку формирует не в соответствии с его должностным статусом, а согласно своим ценностным установкам. Поэтому ему трудно вступать в контакт с коллегами и вышестоящим руководством, к которым он не испытывает уважения. Это приводит его к внутреннему дискомфорту и снижает продуктивность работы. Перечисленные факторы служат способом установления делового контакта. Независимо от испытываемых чувств (симпатии - антипатии) демонстрирует объективную оценку отношений. В общении придерживается правил этикета и ритуала. При обсуждении проблем уделяет внимание мелким деталям. Излагает мысли логично и последовательно. Своей позиции не навязывает, действует методом убеждения. Старается исключить конфликтную ситуацию. В случае возникновения конфликта не умеет отстоять свои позиции, использовать волевые методы давления на партнера по переговорам. Обнаруживает два типа поведения в конфликте: приспособление (уступка) и компромисс. Описанная форма проявления деловой активности не годится для условий жесткой конкуренции или нестабильности производства. Поэтому этот стиль крайне редко встречается в сфере создания материальных благ. Руководитель-воспитатель по своей внутренней психической стр