var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',[_vm._v("ESFJ. Этико-сенсорный экстраверт. Энтузиаст")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общее описание")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Эмоционален и общителен. Умеет поднять настроение, развеселить. С удовольствием рассказывает о своих связях, знакомствах. Эмоции меняются резко: то весел и дружелюбен, то вспыльчив и зол. Следит за манерами и корректностью поведения окружающих. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Заботлив и гостеприимен. Умеет создать уют из того, что есть под руками. Очень следит за самочувствием и здоровьем себя и своих близких. Любит делать подарки, доставлять людям приятное. Прекрасный организатор отдыха и торжественных мероприятий. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. С трудом сдерживает свои эмоции. К людям, которые ему не нравятся, относится придирчиво. Очень обижается, если его старания не ценятся. Довольно суетлив и разбросан. Постоянно перегружен делами, от многих из которых можно отказаться. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Плохо предчувствует жизненные потрясения и драматические события. Ожидает только положительного результата, поэтому часто подвержен стрессу несбывшихся надежд. Устойчив в привычках и способах времяпрепровождения. Переоценивая запас времени, не успевает выполнить все, что запланировано. За необходимую работу часто берется в последнюю минуту, чем обрекает себя на губительную спешку. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Подчинённый.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильные стороны.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Активный, оптимистичный, доброжелательный человек. Внимателен к людям, заботлив. Стремится помочь всем, кто просит об этом. Хорошо чувствует эмоциональное состояние других людей. Легко заводит новые знакомства личного и делового характера. Противник пустой траты времени, всегда занят неотложными делами. Восприимчив к перспективным идеям, которые можно применять на практике. Обаятельный, располагающий к себе собеседник. Обладает хорошим вкусом. Многое умеет делать своими руками. В жизни всего добивается сам, не надеясь на других. Любит все делать быстро, но без свидетелей. Настойчив и решителен в действиях, особенно в экстремальной ситуации, когда против него направлена агрессия или ощущается острый недостаток времени. К советам других прислушивается, но поступает по-своему. С успехом занимается любой практической деятельностью, но любит, чтобы его усилия и находчивость ценили. Стремится к стабильности, высокому материальному уровню жизни, уважению коллег по работе. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проблемы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Накапливает отрицательные эмоции, которые способен некоторое время сдерживать. Но если их слишком много — выходит из себя, переживает, может даже заболеть. Не может действовать без эмоционального контакта с собеседником, поэтому ему трудно взаимодействовать с людьми, которые никак не проявляют своего отношения к происходящему. Неэкономен, не всегда оценивает выгодность и полезность того, что делает. Долго колеблется перед принятием важного решения по сложному вопросу. Плохо рассчитывает время, с трудом отделяет главное от второстепенного, перегружая себя делами. Переоценивает свои деловые способности. Не умеет ждать, ему хочется сделать как можно больше дел и как можно скорее. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("От него нельзя требовать и ожидать: ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • безэмоционального, всегда разумного и объективного отношения к делу, к людям ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • гибкости в поведении и быстрой адаптации к ситуации ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • безвозмездного альтруизма ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • жесткости и бескомпромиссности в отношениях с людьми ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • хороших стратегических и технологических способностей ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • пунктуальности в соблюдении сроков. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендуемый род деятельности ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Налаживание инфраструктуры социума; реклама, социологические опросы: медицина, здравоохранение; дизайн, оформительская работа; сфера обслуживания, торговля; организаторская деятельность в социальной и научной сфере (организация ярмарок, симпозиумов, отдыха, праздничных мероприятий). ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Руководитель")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Представитель данного социотипа характеризуется восприимчивостью к любым идеям, начинаниям. Энергичен, предприимчив, в ситуации риска способен проявить отвагу. Является кипучей, жизнерадостной натурой; душа общества, фантазер, временами — хвастун. Хорошо владеет эмоциями, использует их как инструмент, как способ преодоления препятствий, проявляет в этом изобретательность. Не любит откладывать дела на потом, если видит в них непосредственную выгоду. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("«Энтузиаст» — руководитель-компаньон ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Всякий компаньон хорошо чувствует себя в атмосфере сплоченного коллектива, движимого к единой цели. Руководитель типа «энтузиаст» стремится создать на рабочем месте атмосферу сотрудничества и дружелюбия. Он склонен отождествлять себя с организацией, остро переживает ее неудачи, радуется успехам. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Если «энтузиаст» психологически слаб — старается в структуре коллектива найти такое место, которое обеспечивало бы ему безопасное существование. Более сильные и уверенные в прочности своего положения проявляет ярко выраженное стремление к доминированию, по возможности сохраняя положительные взаимоотношения в рабочей среде. Такие руководители обладают способностью быстро принимать решения. Берутся за работу «засучив рукава», без промедления. Побуждают к этому подчиненных. Данный социотип сам может взяться за самую рутинную работу, если считает, что это нужно, может проявить излишнюю суетливость, опасаясь что-то недоделать. За счет энергичности может конкурировать на достаточно высоком уровне. Однако в условиях жесткой конкуренции не выдерживает гонки. Ему не хватает видения перспективы. Он «утопает» в мелочах. Поэтому наиболее пригоден для руководства на узких участках производства. «энтузиаст», несмотря на свой «компанейский» характер, предпочитает, чтобы его указания и просьбы выполнялись без обсуждения и критических замечаний. В противном случае это чревато конфликтом. В отношениях с начальством достаточно осторожен, хотя в некоторых случаях способен пойти на обострение. Уважает людей самостоятельных, способных отстоять свою точку зрения, проявить характер вне зависимости от ранга. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ключевые характеристики.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Хорошо решает тактические задачи. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Адекватно может проявить себя в качестве фронтального лидера. Способен увлечь других. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Способ установления делового контакта в значительной степени зависит от эмоциональных и ценностных установок. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Форма деловой активности характеризуется настойчивостью в достижении цели путем успешного обхода препятствий и осложнений во взаимоотношениях. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 5. Система взаимодействия с подчиненными строится с учетом эмоциональных взаимодействий. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 6. Склонен к сотрудничеству на основе взаимной выгоды. В его поведении могут проявиться элементы конкурирующего стиля поведения. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 7. Достигает крупных результатов за счет энергичности, умелой мотивации и манипулирования эмоциональными отношениями. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 8. Может манипулировать этическими принципами, ориентироваться на разные «правила игры». ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 9. Мотивы и цель: благие намерения и корыстные побуждения. Цель — получение прибыли (желательно в рамках закона). ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 10. Отношение к закону: предпочитает следовать букве закона. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 11. Стратегия: полная свобода управления в рамках закона.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Условия для работы ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Стимулом, который движет энтузиастом, является достижение высокого эмоционального статуса в группе. Он хочет быть хорошим для всех, для того чтобы иметь возможность оказывать эмоциональное влияние. По этой причине стремится понравиться собеседнику, помочь ему в чем-то. Поэтому при взаимодействии с энтузиастом вам прежде всего необходимо продемонстрировать хорошее отношение к нему, то, что вы несомненно цените и уважаете его мнение. Дайте ему высказаться до конца, так как энтузиаст — тип напористый, и если он вошел в возбужденное эмоциональное состояние, вам вряд ли удастся переубедить его сразу (нужно некоторое время, чтобы он «остыл» и смог трезво порассуждать, изменить свои взгляды). Вы можете только несколько ослабить его эмоциональный прессинг, сохраняя сами уравновешенное состояние и уточняя детали спокойными наводящими вопросами. Он может делать любую, даже неинтересную, но нужную работу с хорошим качеством, если вы сумеете объяснить ее полезность и перспективы развития дела в дальнейшем. Так как энтузиаст — конкретно-мыслящий тип, для него немаловажна хорошая оплата и удобные условия труда. Хотя некоторое время он может и просто помогать вам, если вы — «хороший человек». ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендации по адаптации (самосовершенствованию)")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("В повседневной жизни вы — человек открытый, общительный, дружелюбно настроенный. За это вас любят окружающие. Вы хорошо разбираетесь в настроении людей, сочувствуете чужому горю, умеете сопереживать. Вы не оставите человека в беде, окажете помощь обиженным. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вы умеете доставлять удовольствие людям. Вы гостеприимны, хлебосольны, заботливы. Умеете интересно и наполненно проводить свободное время: ходить в кино, театры, на концерты, часто бываете в гостях. Любите приятные поездки и путешествия. Вы не прочь провести время в веселых, шумных компаниях, любите праздники и застолья. Вам нравится дарить подарки, доставлять людям радость. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ваша основная проблема — неумение распорядиться своим временем в повседневных делах. Вы слишком торопливы, часто суетливы, у вас ни на что не хватает времени. Вы утопаете в бесконечных делах и заботах, часто забывая о главном. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Старайтесь правильно спланировать свой день. Откажитесь от мелких, второстепенных дел и вопросов, отложите их на потом. Не хватайтесь за все сразу, иначе вам не удастся все сделать в сроки. Вы можете оказаться незастрахованным от, неожиданностей. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Старайтесь вычленять главное в сети явлений, прогнозировать его в развитии. Вы бываете слишком требовательны и бескомпромиссны по отношению к близким и знакомым, что служит причиной многих разочарований в людях. Раз обманувшись в человеке. Вы становитесь подозрительны и недоверчивы ко всем другим. В порыве эмоций вы можете преувеличить недостатки других, представить этих людей в черном цвете. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Научитесь соизмерять свои требования с реальной жизнью. Будьте терпимы к людям, снисходительно относитесь к их недостаткам. Помните, что идеальных людей нет. Чрезмерным эмоциональным давлением вы можете разрушить установившуюся гармонию отношений. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Другая ваша проблема — чрезмерная расточительность сил и материальных ресурсов. Неумение контролировать повседневные расходы не позволяет вам создать необходимый материальный резерв. Экономьте свои силы, не беритесь за дела, которые не гарантируют надежной отдачи. Откажитесь от бесчисленных встреч с родственниками и знакомыми, лучше займитесь домашними делами, чтобы выкроить себе время для спокойного отдыха. Старайтесь поддерживать дома более строгий порядок. Избавляйтесь от бесполезных вещей, не накапливайте их. Чем меньше у вас мелких, бесполезных предметов, тем легче вы найдете необходимую вещь в нужный момент. Умерьте свое гостеприимство, не будьте навязчивы в угощениях, старайтесь во всем соблюдать чувство меры.")])])])}
