лтуре, требует соблюдения оправдавших себя методов работы. Охотно приходит на помощь людям в трудные для них минуты, оказывает различные услуги. Хранит верность в семье. На мероприятия не опаздывает. Стремится равномерно распределять дела по времени, постоянно загружен какой-либо работой. Трудолюбив в быту. Проблемы. Болезненно переносит несправедливость и неэтичность. Раним и впечатлителен, на добро и зло отвечает тем же. Хорошо видит чужие недостатки, отличается резкостью оценок, но чаще свое отношение выражает не столько словами, сколько тоном и взглядом. Склонен делить людей на «своих» и «чужих». Не прощает предательства. Мнителен, плохо переносит ситуации неопределенности, не любит частых перемен. Ему трудно определить индивидуальные способности людей, ко всем склонен предъявлять одинаково высокие моральные требования. Не любит, когда кто-то подвергает оценке его качества, так как внутренне не всегда в них уверен. При попытках подавить его может проявлять агрессивность. От него нельзя требовать и ожидать: • всегда позитивного отношения к жизни • ; равнодушного отношения к аморальным поступкам • ; быстрой перемены в убеждениях и оценках • ; восприимчивости к неординарным решениям проблем. Рекомендуемый род деятельности. Социальная сфера. Те области, где требуется взаимодействие с людьми. При этом работа может быть кропотливой, требующей сосредоточенности и концентрации внимания, как-то: медицина и здравоохранение (особенно прикладные области — стоматология, массаж, работа медицинской сестры, санитарки (непосредственное проведение процедур), иглоукалывание, физиотерапия, рентгеноскопия, биохимическая диагностика); товароведение; сфера обслуживания (хранение денег и материальных ценностей); бухгалтерская работа обслуживающего характера, работа кассира. Руководитель Это серьезный, спокойный, но внутренне настороженный по отношению к возможным проявлениям негативных эмоций со стороны окружающих человек. Этические нормы для него — прокрустово ложе, посредством которого оцениваются поступки людей. Критически настроен к людям, нарушающим общепринятые нормы поведения. Негативное отношение к человеку внешне не проявляет до тех пор, пока тот явно не продемонстрирует враждебность. В таких случаях оказывает действенное сопротивление, непоколебимо отстаивая свои жизненные позиции и убеждения. Эта способность «охранять» свое внутреннее пространство от вторжения внешних сил определяет стиль поведения руководителя типа «хранитель». «Хранитель» — руководитель-морализатор Морализаторство такого руководителя — результат критического отношения к поступкам людей, которые, по его мнению, должны соответствовать твердо установленным нормам. Ему, как правило, удается давать справедливую оценку поведения человека, войти в доверие к нему, вникнуть в проблему и дать полезный совет этического характера. Однако при одном условии — человек должен соответствовать его представлениям о нравственных устоях и чувстве долга. Руководитель-морализатор критически настроен не только по отношению к другим, но и к самому себе. Значительное внимание уделяет анализу своих способностей, часто их недооценивает. Поэтому избегает брать на себя всю полноту ответственность, предпочитает разделять ее с кем-нибудь другим. Более приспособлен решать задачи тактического характера. Опасается рискованных мероприятий, способных привести к кризисным явлениям, стремится предотвратить резкую смену обстоятельств. Если такая ситуация назрела, может длительное время колебаться в выборе альтернативных путей решения вопроса. Руководитель такого типа имеет задатки иерархического лидера, способного устанавливать строго определенную систему ценностей и соответствующую структуру отношений. В этой системе он стремится обеспечить стабильность установившихся эмоциональных связей. Достаточно легко определяет неформального лидера в коллективе, умеет на него опереться для поддержания стабильности. Стимулирует деятельность коллектива путем апелляции к чувству долга, призывая соблюдать трудовую дисци