var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',[_vm._v("ENTJ. Логико-интуитивный рациональный экстраверт. Предприниматель")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общее описание")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Предприимчивый человек, который умеет загореться делом. Склонен к инновациям подмечает новые теории и разработки и стремится найти им практическое применение. Непоседлив, часто увлекается походами, туризмом, альпинизмом, бегом и т.п. Смело экспериментирует, опровергая обыденные представления. Всегда знает к чему идет и объясняет цель окружающим. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Не боится идти на риск, полагаясь на свою интуицию. Ему приходят в голову самые необычные идеи, наделен богатым воображением. Хороший изобретатель и игротехник. Всегда работает на опережение, так как знает, что завтра будет поздно. Любит природу, наделяя ее особым смыслом. Отличается познавательными интересами в самых разных областях. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Оптимист, обладает хорошим чувством юмора. Его шутки, однако, не всегда уместны. Испытывает проблемы в надежности человеческих отношений. Из-за неразборчивости в контактах его могут втянуть в авантюру. Беспокоен: много говорит, не обращая внимания на дефицит времени. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Его мало волнует внешний вид, лишь бы дело от этого не страдало. Не придает особенного значения порядку, комфорту вокруг себя. Природа его обычно наделяет выносливостью и здоровьем, которое он стремится поддерживать народной медициной и другими нетрадиционными методами. Не терпит праздного времяпрепровождения, из-за чего не расслабляется даже на отдыхе.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Подчинённый.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильные стороны.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Очень динамичный, подвижный, деятельный человек. Остро ощущает напор времени, считает недопустимым отставание в делах. Большой труженик, с удовольствием занимается делами, приносящими пользу. Любознателен, интересуется новшествами, которые можно применить на практике. Хорошо разбирается в вопросах деловой активности. Видит выгодность или невыгодность, перспективность или бесперспективность тех или иных предприятий. Предлагает неординарные пути увеличения эффективности, может пойти на деловой риск. Во всем стремится к деловой ясности и четкости. Не любит действовать вслепую, без подготовки. Планирует свои мероприятия заранее. Не любит хитрости и уловок, предпочитает договариваться на взаимовыгодных условиях. Часто проявляет себя как спонсор и меценат. Может жить без комфорта, если надо. Даже если ему придется трудно, ныть никогда не станет. Эмоционален, суетлив, неусидчив. Оптимист, постоянно излучает дружелюбие. Активный сторонник здорового образа жизни. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проблемы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Не все могут выдержать его напряженный рабочий режим, который он к тому же склонен произвольно менять, навязывая свой ритм окружающим. Обычно забот оказывается так много, что не может всюду успеть. Часто строит далеко идущие, хотя не до конца продуманные планы. Может чрезмерно рисковать, первым бросается во все сомнительные мероприятия. Очень говорлив, любит рассказывать и обсуждать прочитанное и услышанное. Может одеваться достаточно небрежно, не любит, когда на его внешность обращают пристальное внимание. Нетерпелив по натуре, поэтому часто допускает небрежность в оформлении деловой документации, в работе, требующей скрупулезности и тщательности, «творческий» беспорядок в быту и на рабочем месте. В принципиальных для него вопросах проявляет твердость и неуступчивость. Иногда допускает нетактичность, грубоватый юмор. Неохотно посещает врачей. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("От него нельзя требовать и ожидать:")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • взвешенности и неторопливости в принятии решений ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • тщательности в рутинных делах ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • постоянного порядка в быту и на рабочем месте ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • всегда одинаково ухоженного и опрятного внешнего вида ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • тактичности и психологического чутья. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендуемый род деятельности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Лучше всего Предприниматель реализует себя в инновационных областях, где требуется широкий кругозор, нестандартный подход к делу. Умение адаптировать теоретические разработки к применению на практике. Он может быть неплохим изобретателем и рационализатором. Учитывая высокие темпы его работы и подвижность, можно рекомендовать ему динамичную работу, связанную с командировками. Предприниматели не боятся коммерческого риска, поэтому могут быть первопроходцами, зачинателями новых дел в бизнесе. Они могут заниматься разнообразной работой, если она интересна, перспективна, динамична и не требует особой тщательности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Руководитель")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Человек социотипа «Предприниматель» обладает открытым характером. На лице почти всегда царит «американская улыбка», приглашающая партнера к разговору. Легко вступает в общение с интересующим его человеком, склонен к шуткам и ироническим замечаниям. Жизнерадостный, энергичный и прагматичный человек. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("«Предприниматель» — руководитель-игрок")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Любой игрок стремится выразить себя в игре, получая удовлетворение от самого процесса достижения результата (выигрыша). Эта черта является наиболее существенной у руководителя-игрока, который рассматривает деловую жизнь вообще и свою работу в частности как своеобразную игру. Он любит рисковать, нос расчетом. Не боится вкладывать средства и силы в новшества. При этом ориентируется на перспективы, не ожидая сиюминутной отдачи. Стремится использовать все шансы, поэтому работает по многим направлениям, надеясь на крупную отдачу в будущем. Движущим стимулом такой деятельности выступает стремление приобрести известность в качестве победителя. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Трудности, возникшие на его пути, воспринимает спокойно, как нечто само собой разумеющееся. Прирожденный предприниматель. Склонен менять в течение своей жизни несколько профессий и занятий. Во всем стремится разобраться сам, все испытать на себе. Целеустремлен и неутомим. Люди, которые в своей жизни исповедуют принцип удовольствия и стремятся получать желаемый результат без напряжения, не имеют у него авторитета. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Приобретенные материальные средства ЛИЭ не накапливает, а сразу же пускает в дело. Лучше реализует себя как предприниматель, чем как потребитель. Пытается извлечь пользу там, где другие ее не видят. Хорошо высчитывает свободные зоны предпринимательства на рынке, предвидит возникновение новых потребностей, которые породят будущий спрос. В бюрократическом обществе деятельность этого социотипа наталкивается на многочисленные препятствия и ограничения. ЛИЭ стремится анализировать факты, имеющие отношение к его делу, прислушивается к новым идеям. Его интересует та область предпринимательской деятельности, где теория соединяется с практикой. Тяготеет к эксперименту. Хочет узнать, что получится, если предпринять нестандартный шаг. Эксперимент организует таким образом, чтобы обнаружить противоречия, контрастные отличия, альтернативные линии развития. Противопоставляет полученные результаты эксперимента устоявшимся представлениям. ")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("ЛИЭ способен рисковать, полагаясь на интуитивное предвидение исхода дела. Несмотря на то, что большая часть вложенных средств иной раз пропадает даром, уверен, что какая-то их доля окажется направленной на перспективную отрасль и в будущем окупит вложенные капиталы. Способен резко изменить направление движения к цели, что делает его непредсказуемым для конкурентов. Не любит потерь времени, праздного времяпрепровождения. Остро переживает каждый упущенный шанс. Может эффективно жить и работать только в движении, постоянном обновлении. Противник застоя и консерватизма. Не всеми такое поведение воспринимается положительно, так как при этом нарушается устоявшийся порядок вещей. ЛИЭ ни под кого не подстраивается. Его темп жизни может выдержать только тот человек, который не теряет самообладания ни при каких обстоятельствах. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ключевые характеристики")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Склонен к решению стратегических задач. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Адекватно может себя реализовать в качестве фронтального лидера. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. В установлении делового контакта ориентирован на объективные критерии. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Форма деловой активности разнообразная: проявляет трезвый расчет, предусмотрительность, способен на волевое преодоление преград, нетерпим к бюрократизму. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 5. Система взаимодействия с подчиненными основана на стимулировании их интереса: поощряет тех, кто способствует успеху организации, наказывает тех кто препятствует. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 6. Сотрудничающий тип поведения. При необходимости может с успехом участвовать в конкуренции. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 7. Достигает крупных производственных результатов за счет предприимчивости, ориентации на новые технологии, учета объективных факторов. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 8. Близок к моральному стилю менеджмента. Но по необходимости может принять любой стиль. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 9. Мотивы и цель: удовлетворение получает от самого процесса работы. Главная цель — достижение крупного результата, выделяющего его среди других. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 10. Отношение к закону: уважает закон, если он не защищает бюрократические порядки и не мешает проявлению инициативности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 11. Стратегия: свобода управления, максимальное внимание к производству, новым технологиям, человеческим ресурсам.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Условия для работы")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Стимулом для Предпринимателя является стремление к уникальности. Поэтому работа должна быть увлекательной для него, давать возможность проявить изобретательность. Предприниматели также обожают учиться — всему, что рано или поздно может оказаться полезным для дела. Поэтому, если вы будете почаще посылать такого сотрудника в командировки, на семинары, симпозиумы, конференции, курсы повышения квалификации и переподготовки, он будет удовлетворен и заинтересован такой работой. В работе с Предпринимателем следует принимать во внимание его нелюбовь к тщательной, рутинной работе. Поэтому вам следует исключить ее из круга его обязанностей, как и работу, связанную с кропотливым оформлением. Следует также с некоторой осторожностью относиться к рискованным предложениям Предпринимателя, уточнить, есть ли достаточная материальная база у затеваемого мероприятия и кто его будет финансировать. Неплохо также поинтересоваться, какие люди оказывают Предпринимателю поддержку (часто вся его «команда» — это он сам). Если он торопит Вас действовать немедленно, иначе будет поздно, — это необходимо принять во внимание, ведь Предприниматели хорошо чувствуют своевременность действий. Однако сами действия следует осуществлять по принципу «семь раз отмерь...», так как зачастую деловая активность Предпринимателя настолько велика, что может перерастать в суетливость. Предприниматели склонны делать намного больше того, что требуется по ситуации, вовлекая при этом всех окружающих в лихорадочную спешку. Если он горячится, проявляет нетерпеливость и нетерпимость к «волокитчикам», это следует воспринимать спокойно, ровно и доброжелательно, демонстрируя понимание и внутреннюю поддержку его затруднениям. А если Вы еще будете оказывать содействие его деловым начинаниям, удерживая его при этом от рискованных действий и расходов, Ваше продуктивное взаимодействие гарантировано! ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендации по адаптации (самосовершенствованию)")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ваша сильная сторона — это предприимчивость в новых делах и начинаниях. Новое, нужное дело Вы начинаете с большим азартом, не жалея сил для достижения результата.")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вы — человек практического ума, умеете сориентировать любую теорию или идею на практическую пользу. Вы принимаете деятельное участие при внедрении в жизнь новых технологий. Вы не склонны размениваться по мелочам, Вас особенно привлекают крупные цели и проекты.")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вас можно назвать человеком действия, так как Вы не откладываете выполнение своих решений в долгий ящик. Вы быстро, без раскачки включаетесь в новое дело и стараетесь довести его до конца. ")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вы — оптимист, верящий в успех начатого дела. Своими планами Вы охотно делитесь с окружающими; любите испытывать себя в различных сферах деятельности, смело идете на риск, готовы к преодолению любых трудностей.")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("В жизни Вы постоянно сталкиваетесь с проблемами в организации повседневного быта. Для отдыха у Вас часто не хватает времени. Несмотря на приверженность к здоровому образу жизни, Вы нерегулярно заботитесь о своем здоровье. Совмещайте полезное с приятным, не сосредоточивайтесь чрезмерно на каком-то одном занятии. ")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Соблюдайте рациональный режим жизни, совмещая работу с отдыхом. Займитесь оздоровительным бегом. Будьте более внимательны к своему внешнему виду, не пренебрегайте мнением окружающих о Вас. Уделяйте больше внимания своим домашним делам. Проявляйте чаще заботу о близких, посвящайте им больше своего личного времени, делайте им приятные сюрпризы, вникайте в их проблемы.")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Старайтесь воспитывать в себе эстетический вкус. Не приносите в жертву свой внешний вид даже во имя целей, которые кажутся Вам более важными. Не злоупотребляйте экстравагантными выходками, особенно осторожно касайтесь интимных тем, не стремитесь обратить на себя внимание таким образом: окружающие могут неправильно Вас понять. Сами того не желая. Вы можете нарушить непринужденную атмосферу общения, поставив себя и друзей в неловкое положение. ")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Другая Ваша проблема связана с двусмысленным юмором и шутками, которые не всегда могут быть правильно восприняты и поняты. Будьте тактичны и скромны в своих высказываниях. Не стремитесь поднять людям настроение любой ценой. Помните, что не все любят шутливый тон, а тем более иронию или насмешки. Избегайте излишней эмоциональной фамильярности в общении, воспитывайте в себе вежливость и хорошие манеры. Это повысит Вашу самооценку и вызовет уважение окружающих. ")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Не идите на конфронтацию по несуществующим вопросам — этим Вы можете противопоставить себя большинству и потерять уважение. Не давайте поспешного выхода своим эмоциям, не допускайте, чтобы Ваши действия опережали мысли. Лучше удалитесь от объекта Вашего раздражения на время и, успокоившись, все обдумав, выскажите ему свое мнение при очередной встрече.")])])])}
