var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_vm._v(" INTJ. Логико-интуитивный рациональный интроверт. Аналитик Общее описание 1. Обладает развитой логикой и способностью к анализу. Умеет четко отделить главное от второстепенного. Структуры, классификации, концепции — его стиль. Объективен и резок в суждениях и принципиальных оценках. Не устраивающие его правила или указания игнорирует. 2. Разобравшись в проблеме, выдвигает принципиально новые решения. Терпим к другим точкам зрения и стилям жизни. Его поведение часто непонятно окружающим, так как многие решения он принимает интуитивно — по догадке. Часто имеет какое-либо увлечение, в котором постоянно совершенствуется. Беседу поддерживает только тогда, когда она касается значимых для него тем. 3. Плохо разбирается в чувствах людей. Упрям и негибок в отношениях. Скрытен, очень не любит непрошеных гостей, не умеет их занять. Осторожен в общении, часто держится на дистанции. Чем больше симпатизирует человеку внутренне, тем меньше демонстрирует свою заинтересованность внешне. Упорно сохраняет ту систему отношений, к которой привык. 4. В быту скромен, согласен довольствоваться минимумом удобств. Повседневные домашние обязанности его угнетают. Несмотря на упрямство плохо развиты пробивные способности. Не умеет подчинять людей. В критических ситуациях довольно нерешителен. От него ничего не добьешься принуждением. Подчинённый. Сильные стороны. Имеет хорошее аналитическое мышление, сильно развитую логику. Умеет выделить главное и отсечь второстепенное в анализируемом материале. Любит обобщать факты, хорошо разбирается в схемах, классификациях и структурах. Реально оценивает практические возможности предпринимаемого дела, не упускает из виду альтернативы. Сторонник дисциплины и порядка, основанных не на административном нажиме, а на совести, долге, личной ответственности. По натуре добр, но дело ставит выше личных интересов и отношений. Человек слова, ценит справедливость, разумный порядок во всем. Хороший докладчик, умеющий кратко и доходчиво донести материал до слушателя. Стремится к независимости, но дает собой руководить людям, умеющим уговаривать мягко, но настойчиво. Хорошо чувствует время, на мероприятия и встречи обычно приходит пунктуально. Осторожен, чувствует опасность. Редко выходит из себя. Демократичен, терпим к иным точкам зрения. Проблемы Недостаточно гибок в отношениях. Предпочитает говорить о том, что его интересует, посторонние подробности в речи собеседника пропускает мимо ушей. Если тема ему неинтересна, старается уйти от разговора, так как не желает тратить время попусту. Не любит делать несколько дел сразу, особенно в спешке. Может проявлять упрямство и бескомпромиссность. Не терпит, чтобы им командовали, хладнокровно отвергает волевое давление. Скрытен, не любит обсуждать свою личную жизнь. Не терпит, когда ограничивают его свободу, выражают необоснованные требования. От него нельзя требовать и ожидать: • большой практичности и мобильности • ; чувствительности, тонкой пристройки к собеседнику • ; качественного выполнения рутинной работы, неинтересной для самого аналитика • ; коммуникабельности • ; «пробивных» качеств. Рекомендуемый род деятельности. Научно-исследовательская и аналитическая работа (анализ сложных явлений и объектов). Разработка новых идей для практического применения. Может работать хорошо везде, где требуется системный анализ, построение схем и классификаций, выдвижение концепций. В американских разработках этот тип называют «реорганизатор бизнеса». Руководитель. Представитель этого социотипа — человек, демонстрирующий слабую эмоциональность в общении, спокойствие, уравновешенность, взвешенность суждений. В любом случае не изменяет правилам корректного поведения. Часто бывает скрытным, имеет мало друзей, предпочитает жить своими интересами и по своим правилам. По этой причине многими воспринимается как отрешенная от житейских проблем личность. «Аналитик» — руководитель-объективист Человек, претендующий на объективность: стремится максимально абстрагироваться от эмоциональных переживаний по тому или иному поводу, найти объективные основания для своих поступков и деятельности, увидеть главную стержневую линию развития событий, изложить свое видение проблемы в системе. Все это — существенные черты руководителя-объективиста. «аналитик» стремится к всестороннему рассмотрению объекта — только таким путем, по его мнению, можно получить объективную картину происходящего. Итогом этих размышлений являются ответы на вопросы «почему ?», «зачем ?», «для чего ?». Аналитик раскладывает объект на составляющие и таким путем объясняет механизм действия сложных образований. Благодаря выработанному подходу способен разобраться в самых сложных проблемах. Свое мнение по тому или иному вопросу не навязывает, не использует при этом свое служебное положение. Опирается на метод убеждения, который основан на четких, логических высказываниях, призванных обосновать необходимость и справедливость принятого решения или выработанной позиции. Дает понять, что его оценка объективна. Изменить свое мнение может только в том случае, если ему предложат более аргументированную позицию. Бескомпромиссность проявляет только в том случае, если речь идет о принципиальном производственном вопросе, научной и социальной истине. В мелких вопросах уступчив. Нерадивые подчиненные могут этим воспользоваться. Однако, если не будет соблюдена мера, руководитель-объективист способен на суровые административные наказания, вплоть до увольнения. В этом отношении предпочитает либо ограничиться указанием на возможные последствия, либо предпринимает радикальные шаги. Умеет найти подход к людям, вызвать интерес к себе или понимание своих идей. Неплохой преподаватель, так как поощряет даже небольшую инициативу, выслушивает все точки зрения, дает возможность раскрыться возможностям человека, хорошо чувствует людей, находящихся в поле зрения, например, когда выступает перед аудиторией. Хорошо ощущает реакцию слушателей на свои слова. Если надо, может быстро перестроится по ходу дела, повернуть обсуждение в неожиданное русло. Чем больше аудитория, тем увереннее он себя чувствует. При общении же с человеком, который не видит перспектив, слишком заземленным, испытывает затруднения. В беседу на не интересующие его темы не вступает и не поддерживает бесполезный с его точки зрения разговор. Ключевые характеристики 1. Успешно решает стратегические задачи. 2. Адекватно может себя проявить в качестве фронтального лидера. 3. В установлении делового контакта ориентируется на объективные критерии и оценку с этих позиций потенциальных возможностей партнера. 4. Деловая активность характеризуется предусмотрительностью с ориентацией на анализ собственных возможностей. 5. Система взаимодействия с подчиненными строится на коллегиальной основе. 6. Сотрудничающий тип поведения с элементами компромиссного типа. 7. Достигает крупных результатов за счет учета объективных возможностей. Эмоционально воздействовать на людей не может. 8. Стиль управления согласуется с общепринятыми этическими и профессиональными нормами поведения. 9. Мотивы — преуспевание в рамках этических представлений 10. цель — прибыль в рамках закона. 11. Отношение к закону: не может работать по законам, которые защищают бюрократические порядки, регламентируют иерархическую структуру полномочий. 12. Стратегия: полная свобода управления, сообразующаяся с объективной необходимостью. Не может выполнять функции лидера в решении этических проблем. Условия для работы Оптимальным для аналитика является спокойный режим работы, позволяющий обдумывать логические вопросы. При этом работа должна быть интересной для него самого (лучше всего дать ему возможность удовлетворять собственное любопытство). Для него лучше всего работать независимо от других, в «своем» темпе. При этом статус, стремление выделиться, преуспеть, сделать карьеру являются второстепенными для аналитика. Побудить его к работе могут эмоциональные пр