ннисом, горными лыжами, автомобилизмом, боксом. Обязательно чередуйте умственный труд с физическим. Вам рекомендуются упражнения на концентрацию внимания, а также занятия, которые развивают это качество, например сбор грибов, ягод, ловля рыбы. Вы нуждаетесь в моральной поддержке своих начинаний. В вашем окружении должны быть люди решительно и оптимистически настроенные, которые бы вас время от времени подталкивали, помогали преодолеть пессимизм и неверие в свои силы. Другая ваша проблема — неумение общаться с людьми, далекая психологическая дистанция, которая воспринимается окружающими как сухость, черствость, отчужденность. Иной раз вы сознательно обрекаете себя на одиночество. Не будьте мрачным и угрюмым. Чаще улыбайтесь, будьте приветливы, но не заходите слишком далеко — вам трудно отказать людям. Осторожно выбирайте круг необходимых для вас контактов, чтобы избежать переутомления и ненужных вам этических проблем. Старайтесь не терять налаженных эмоциональных связей. Вы многим можете помочь своей рассудительностью и пониманием. Учтите, что не все могут так же успешно анализировать события и абстрагироваться от эмоций, как вы. Старайтесь воспитывать в себе нормы общепринятой этики и следуйте им повседневно. Считайтесь с интересами других людей. Старайтесь глубже вникать в проблемы каждого, если хотите, чтобы они с вами сотрудничали. Помните, что человека обижает поверхностное, невнимательное отношение к нему. ")])])}
