var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',[_vm._v("ENFP. Интуитивно-этический экстраверт. Советчик")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общее описание")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Проницателен: хорошо видит мотивы, которые движут людьми. Тянется к способным и неординарным личностям. Умеет ободрить человека, вселить надежду. Советует, как найти выход из сложных жизненных ситуаций. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Альтруистичен: для людей, которым симпатизирует, может сделать больше, чем от него ждут. Легок и доброжелателен в общении. Своей искренностью и теплотой располагает к доверию. Обижается, если не получает эмоционального отклика. Стремится руководить чувствами окружающих. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. В экстремальных ситуациях активизируется. Дает отпор необоснованным нападкам. Ему не хватает воли, чтобы заставит себя выполнить то, что объективно необходимо. Плохо соизмеряет желаемое с реальным положением дел. Проявив инициативу, стремится найти людей, которые обеспечат внедрение его идей без его участия. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Очень устает от рутины, поэтому часто меняет свои увлечения. Ему не дается кропотливая работа: плохо отслеживает детали, цифры, параграфы. С трудом соблюдает временной график, часто опаздывает. Не подчиняется строгой дисциплине. Не уважает формальную субординацию. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Подчинённый.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильные стороны.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общителен и эмоционален. Способен найти подход к любому человеку в случае необходимости. В этом ему помогает природная обаятельность и дипломатичность. Говорит комплименты, стремится делать людям приятное. Разбирается в способностях людей. Хорошо чувствует их скрытые побуждения и недостатки. Способен даже предсказывать поведение окружающих. Тянется к незаурядным людям, интересуется всем необычным и загадочным. Увлекшись какой-либо идеей, охотно ее рекламирует, становясь ее активным сторонником. Впечатлителен и отзывчив, сочувствует людям, любит давать им советы в сложных жизненных ситуациях. Эмоционален, нуждается в постоянном притоке новых впечатлений. Стрессоустойчив, в экстремальных условиях мобилизуется, может дать решительный отпор. В случае неудачи переживает ее бурно, но недолго. Любит красивые вещи, стремится оригинально одеваться. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проблемы.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Из-за желания поддерживать со всеми хорошие отношения, перегружает себя лишними контактами. Доверчив по натуре, в разговоре может порой рассказать лишнее. Быстро разочаровывается в объемах своих симпатий. Может быть экзальтированным, сильно зависит от настроения. Не любит регламентации и строгих правил, стесняющих его независимость. Противник иерархии и чинопочитания. Не приемлет условностей и предрассудков. Довольно неорганизован и непрактичен. Часто не успевает сделать намеченное к сроку, так как склонен отвлекаться на второстепенные дела. Порой ему не хватает терпения и тщательности, не всегда доводит начатое до конца. Осуждает грубых и невоспитанных людей, однако сам бывает вспыльчив, несдержан, порой даже агрессивен. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("От него нельзя требовать и ожидать:")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • качественного выполнения кропотливой работы ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • систематического ведения документации ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • высокой самоорганизованности ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • умения эффективно руководить другими людьми и распределять обязанности ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • безэмоционального поведения в конфликтных ситуациях. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендуемый род деятельности.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Гуманитарная сфера: педагогика; искусство; журналистика; семейное психологическое консультирование; служба знакомств; имиджмейкерство; реклама. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Руководитель")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Человек этого стиля обладает живым, но не методичным умом, импульсивной и непостоянной активностью. Он может работать лишь тогда, когда его желание совпадает с необходимостью. Нуждается в многочисленных и разнообразных друзьях вне дома. Он мало считается с допущенными в прошлом ошибками, говорит и делает то, что удобно в данный момент. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("«Советчик» — руководитель-альтруист")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Альтруистом называют личность, для которой интересы другого человека или социального окружения являются наиболее значимой ценностной мотивацией. «Психолог» в качестве руководителя способен проявить себя в деятельности, искренне направленной на интересы коллектива, не преследуя при этом никаких корыстных целей. Сильной стороной руководителя-альтруиста является проницательность, умение проникать в суть человека, видеть различные стороны его характера. Быстро и легко определяет место человека в психологической структуре коллектива, его совместимость-несовместимость с ним. Способен предвидеть развитие человеческих взаимоотношений и делать из этого соответствующие выводы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("При установлении делового контакта с партнером в значительной степени ориентирован на личностный фактор. Если партнер нравится — демонстрирует обходительные манеры. Способен тонко и умело управлять своими эмоциями в целях достижения положительного результата. Эмоциональность такого человека воспринимается как непосредственная и ненавязчивая. Это вызывает положительный эмоциональный отклик, что способствует решению многих проблем. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Система взаимодействия с коллегами и подчиненными ориентирована на налаживание дружеских контактов и сохранение положительной психологической атмосферы. При назревании и возникновении конфликтной ситуации берет на себя инициативу в разрешении этических проблем. Умеет расположить к доверительной беседе, снять напряженность. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Форма деловой активности носит импульсивный характер. Работоспособность в значительной степени зависит от отношения к делу. Не выносит рутинной, монотонной работы. Необходимость соблюдать строгую, повторяющуюся последовательность действий его раздражает. Предпочитает действовать по наитию, без скрупулезного логического анализа фактов. Эти качества обусловливают то, что такой тип руководителя крайне редко встречается в производственной сфере, чаще в культурно-досуговой и сфере обслуживания, где очень важны свойства эмоциональной непосредственности и ориентации на нужды человека. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Склонен к делегирующему стилю управления, т.е. к добровольной передаче ответственности за некоторые аспекты деятельности. Делает это для того, чтобы освободить себя от работы, которую считает рутинной. За собой оставляет вопросы, требующие стратегического подхода. Хорошо видит перспективу. Не унывает от случившихся неудач, надеется на удачу и благоприятное стечение обстоятельств. Характеризуется компромиссным типом поведения. Уступчив, если видит, что человек не преследует корыстных целей. Однако если поступки партнера продиктованы явно эгоистическими соображениями, руководитель-альтруист может проявить упрямство, иногда граничащее с авантюризмом. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ключевые характеристики ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Склонен к решению стратегических задач. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Адекватно может себя реализовать в качестве фронтального лидера. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Способ установления делового контакта ориентирован на личностный фактор. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Форма деловой активности основана на предусмотрительности, своевременности поступков, видении перспективы. Подвержен подъему и спаду настроения (импульсивная работоспособность). ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 5. Система взаимодействия с коллегами и подчиненными ориентирована на личностный фактор. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 6. Компромиссный тип поведения с элементами сотрудничающего типа. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 7. Достигает крупных результатов за счет оптимально подобранного коллектива, создания в нем благоприятного климата, удачного делегирования полномочий. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 8. Соблюдает принцип этики в бизнесе. В этом плане подвержен влиянию партнеров по деловой активности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 9. Мотивы: благие намерения, сочетаемые с желанием личного успеха. Цель — реализация своих способностей, самоутверждение посредством материального достижения успеха. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 10. Отношение к закону: предпочитает следовать букве закона, если последний не сковывает инициативу. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 11. Стратегия: полная свобода управления, максимальное внимание к человеческому фактору. Может взять на себя функции лидера в разрешении этических проблем. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Условия для работы")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Советчиком движет стимул уникальности. Он стремится как можно больше знать в гуманитарной сфере, повышать свою компетентность, с удовольствием занимается самообразованием. Ему нравится работа, требующая разнообразных контактов с людьми, постоянных переключении. Поэтому если вы будете поручать ему общение с клиентами, а также дипломатическое взаимодействие с партнерами или другими подразделениями фирмы, это наилучшим образом задействует его потенциал. Если вы воздадите должное уникальным способностям и знаниям Советчика, отметите их и будете присушиваться к его советам, вы войдете к нему в доверие. Однако руководствоваться рекомендациями Советчика нужно с некоторой осторожностью. Помните, что, несмотря на сильную интуицию, это тип несколько непрактичный, нереалистичный и даже инфантильный. Советчики не очень сильны в повседневных, рутинных делах, требующих внимательности и тщательности. Они с удовольствием перепоручают и предоставляют эту работу любому, кто только захочет за нее взяться. Поэтому если вы окружите Советчика заботой в конкретных делах, лояльностью к его бытовой непрактичности и терпимостью к его небрежности в мелочах — это станет важными слагаемыми вашего успешного взаимодействия. Если Вы, к тому же, предоставите ему свободный режим работы и станете всячески способствовать его саморазвитию — Вы будете полностью отвечать его понятиям об идеальном начальнике. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендации по адаптации (самосовершенствованию)")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильной стороной Вашей личности является хорошо развитая интуиция, позволяющая Вам безошибочно определять скрытые возможности и способности других людей. Ваша проницательность особенно остро отмечает отрицательные задатки в людях. Больше доверяй своему первому впечатлению, так как Ваша доброта и снисходительность могут заставить Вас несколько идеализировать людей при более близком знакомстве. Вы умеете проявлять чуткость и внимание к окружающим, стараетесь помочь им разобраться в личных проблемах, сделать им приятное. Замечая таланты и достоинства людей, Вы делаете им удачные комплименты, стараясь тем самым поднять самооценку каждого, улучшить его настроение, а также вселить уверенность в достижении желаемого результата. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Другой Вашей сильной чертой является умение устанавливать с людьми теплые, доверительные отношения, что располагает их к откровенности. Эту откровенность Вы стараетесь употребить им на благо — помочь своими дипломатическими советами выпутаться из трудных жизненных ситуаций, избежать насмешек и поспешных решений, о которых пришлось бы пожалеть в будущем. С другой стороны, если человек не оправдал Вашего доверия или вызвал антипатию, Вы воздвигаете между ним и собой невидимый барьер и стараетесь отдалиться от него. Ваши антипатии бывают так же сильны, как и симпатии. Но чаще всего Вы умело поддерживаете с людьми гармоничные и ровные отношения. Вы — отличный дипломат. Вашей слабой стороной является неумение заставить себя заниматься долгой кропотливой работой, следовать установленным правилам и регламенту, тщательно вести документацию, подробно прорабатывать детали, работать в тесноте, при большом скоплении людей, а также «уважать» иерархию независимо от индивидуальных качеств начальства. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вам трудно дается беспристрастный логический анализ, отделение главного от второстепенного, из-за чего Вы склонны утопать в бесконечных делах и смене увлечений. Вам также трудно правильно распределить мероприятия по времени, что ведет, в конечном счете, к физическому и нервному переутомлению и неудовлетворенности жизнью. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вам можно рекомендовать следующее: отработайте гибкий, но четкий распорядок дня и старайтесь его соблюдать. Планируя мероприятия на завтра, обязательно определяйте важность каждого из них. Доверяя своей наблюдательности, помните, что она у Вас распространяется лишь на людей, а в деловой сфере прислушивайтесь к советам профессионалов, иначе неизбежны обвинения в верхоглядстве. Больше и вдумчивее читайте, записывайте наиболее важные правила и положения. Помните, на одной импровизации и общем представлении в эпоху НТР далеко не продвинешься. Другой Вашей проблемой является недостаточно развитая сила воли, неумение держать себя в руках при любых обстоятельствах. Вы часто колеблетесь при принятии важных решений, а неожиданные вспышки агрессивности могут испортить Ваши отношения с людьми. Хотя Ваша смелость и решительность в экстремальных ситуациях часто выручает Вас, но в спокойном состоянии Вы периодически ощущаете апатию, Ваш тонус падает. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Воспитывайте в себе сдержанность и последовательность, заставляйте себя делать все более тщательно и старайтесь доводить начатую работу до конца. Старайтесь не давить на окружающих без явного на то повода, предлагая навязчивые советы, требуя от них немедленного выполнения чего бы то ни было. Старайтесь управлять своими эмоциями. Вам очень полезны занятия аутотренингом. Если чувствуете, что конфликт неизбежен, старайтесь немедленно прекратить общение, чтобы не сожалеть впоследствии о сказанном или сделанном. Не нервничайте по пустякам, не сосредоточивайтесь на своих переживаниях, так как это может отрицательно сказаться на Вашем здоровье. ")])])])}
