т сам и не любит, когда при нем критикуют других людей. В коллективе занимает позицию золотой середины. Проблемы. Ему с трудом дается напряженная деловая активность, трудно долгое время быть подвижным, динамичным. Бывает расслаблен и несобран. Скрытен и раним. Может проявлять явное недовольство, если его покой нарушают. В такие минуты может возмутиться, резко ответить. Довольно ленив: если работа мало его интересует, не обещает никакой выгоды лично ему, может симулировать. Не видит смысла в пустой суете и напрасной трате сил. Не любит преувеличений, нуждается в фактах и доказательствах. Старается избегать начальства, уклоняется от скучных, сугубо деловых разговоров. Ему трудно проявлять волю и настойчивость, отстаивать интересы дела. Плохое самочувствие сильно снижает его работоспособность. Часто делает вид, что многое может сделать, хотя это и не всегда возможно. От него нельзя требовать и ожидать: • постоянно высокой деловой активности • последовательности и организованности в делах • бескомпромиссного отстаивания интересов дела • умения руководить большим коллективом, распределять обязанности • участия в рискованных атакующих операциях. Рекомендуемый род деятельности. Лучше всего посредник реализует себя в социальной сфере. Может хорошо налаживать и поддерживать коммерческие контакты, успешно работает там, где нужно обслуживать конкретного, отдельно взятого человека. Посредник — это специалист по налаживанию и поддержанию инфраструктуры социума. Поэтому он — лучший снабженец, торговец мелким оптом, коммерческий директор небольшой фирмы, дипломат-менеджер. Неплохо работают посредники в области рекламы и издательства, медицине, сфере услуг. Из них получаются неплохие оценщики качества, дизайнеры, организаторы отдыха. Руководитель Человек этого типа общителен, дружелюбно настроен по отношению к окружающим. Проявляет искренний интерес к людям, которые отвечают ему взаимностью. Испытывает потребность помогать другим. «Посредник» — руководитель-дипломат Дипломат должен