var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',[_vm._v("ESTP. Сенсорно-логический экстраверт. Маршал")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общее описание")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Решительный и целеустремленный человек. Подчиняет людей своему влиянию. Предпочитает только крупные цели — идет к ним несмотря ни на что. Гибкий тактик, не пренебрегает временными компромиссами. Его настойчивость возрастает пропорционально количеству препятствий, которые нужно преодолеть. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Умеет сконцентрировать все свои силы в решающий момент. Логически просчитывает план действий наперед, словно играет партию в шахматы. Стремится давать другим указания, но сам приказного тона не выносит. Дела контролирует по конечному результату. Обычно занимает позицию теневого лидера. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Недоверчив, людей проверяет в деле. В первую очередь концентрирует свое внимание на недостатках и слабых местах. Плохо предвидит будущее: останавливается в нерешительности, если события выходят из под контроля и принимают неожиданный оборот. Если нет азарта борьбы, погружается в сомнение и плохое настроение. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Ему трудно сдерживать отрицательные эмоции в отношениях с людьми. Его чувства всегда имеют физический оттенок обладания партнером. Не уверен в том, как к нему относятся окружающие. Бывает нетерпим в общении с близкими. В экстремальной ситуации не оставляет места жалости и сочувствию. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Подчинённый. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильные стороны.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Отличается лидерским характером. Человек волевой, мобилизованный, энергичный. Обладает быстрой реакцией, сочетающейся с хорошей выдержкой. Умеет управлять людьми, быстро ориентируется в изменившихся обстоятельствах. Способен быстро принимать кардинальные решения. Настроен на реальные близлежащие цели — гибкий тактик. Энергичен и деятелен, в периоды подъема развивает незаурядную работоспособность. Имеет критический склад ума. Активно отстаивает интересы свои и своей группы. Стремится к высокому материальному уровню жизни. Любит действовать по-крупному, все тщательно взвесив и разработав конкретный план действий. Очень целеустремлен и практичен в делах. Живет разумом, дело ставит выше личных симпатий. Стрессоустойчив, мобилизуется в экстремальных ситуациях. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проблемы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Довольно агрессивен, ему с трудом даются ровные, бесконфликтные отношения с окружающими. Не любит нерешительных людей, уклоняющихся от смелого действия. Не выносит приказного тона: если на него оказывают давление, дает сильный отпор. Самолюбив, в гневе проявляет нетерпимость к чужим промахам и ошибкам. Неугодным людям может создать неблагоприятную обстановку. В личных отношениях неуступчив, все хочет устроить по-своему. Недоверчив к новым людям, стремится при помощи осторожных вопросов выяснить их положение, связи и возможности. Идет к своей цели, не считаясь с интересами других. У него бывают периоды спадов и подъемов настроения и работоспособности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("От него нельзя требовать и ожидать: ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • последовательности во всем ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • мягкости, уступчивости, неагрессивности ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • того, что он будет работать бесплатно, «за идею» ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • прогностических способностей. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендуемый род деятельности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Маршал хорошо руководит другими людьми, распределяет обязанности. Способен в деталях продумать необходимую организационную структуру и последовательно внедрить ее. Может не только тщательно отобрать людей, проверив их предварительно в деле, но и держать всех своих подчиненных в жестких рамках, прибегая по необходимости к непопулярным мерам, волевому воздействию. Практичен, хорошо разбирается в выгодности затеваемого дела. Может хорошо работать руками, мастерить что-либо. Оптимальное применение маршалы находят в области управления, техники, милиции, военном деле. Из них получаются неплохие руководители разных уровней, вплоть до самых высоких. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Руководитель.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сущностью социотипа «маршал» является установка на преодоление преград и препятствий, целенаправленная борьба с теми, кто мешает достижению цели. Испытывает потребность влиять на людей, контролировать их желания и поступки. Общителен, заводит дружеские отношения с людьми, на которых опирается в процессе деловой активности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("«Маршал» - руководитель-боец")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Волевой, уверенный в своих силах человек. Не боится опасности, риска в деловой активности. Успешно может выступать в роли менеджера, предпринимателя. Умеет быстро оценить волевые качества людей. Дифференцирует их по категориям, вырабатывая к каждой определенное отношение: ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • А) конфронтирующее ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • Б) равнодушное ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • В) благожелательное ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • Г) близкое, дружелюбное. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Умело устанавливает соответствующую психологическую дистанцию. Информация о волевых качествах окружающих его людей облекается у слэ в конкретные логические планы, расчеты, проекты, диспозиции. Умеет расставлять людей по местам в зависимости от их потенциальных способностей, необходимых для достижения главной цели. Результат активной деятельности слэ — это всегда сплоченная группа, повинующаяся его направляющей руке. Умело оценивает расстановку сил, сложившуюся ситуацию. В соответствии с этой оценкой гибко лавирует. В позиционной борьбе никогда не упускает инициативу, даже если применяет выжидательную тактику. Выбирает момент для решительных действий, при необходимости идет на разумный риск. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Система организации взаимодействия с людьми строится в соответствии с его склонностью к иерархическому лидерству. Тяготеет к скрытым, теневым формам лидерства. Стремится находится как бы в стороне от событий, не упуская их из поля своего зрения. Вмешивается в те решительные моменты, когда дело необходимо повернуть в нужное русло. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Установление делового контакта имеет у данного социотипа свои особенности. Для него большое значение может иметь внешний облик человека, его одежда, манера себя вести, умение владеть собственным телом. К людям, слабым физически, не развивающем себя и не закаливающим свою волю, относится критически. Его отличает уважение к пробивным качествам человека. Ценит прежде всего устремленность на достижение цели, а не компетентность. Все эти факторы для слэ являются объективными критериями при установлении делового партнерства.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Форма деловой активности характеризуется динамичностью, внешне не всегда выраженной. Проявляет неторопливость, ждет необходимой для решительных действий ситуации, завершенность которой является сигналом для проявления волевых качеств, возрастающих пропорционально количеству преград и препятствий. Успешное завершение дела, сопряженного с азартом и напряженной борьбой, доставляет слэ удовольствие, пробуждает интерес к жизни. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("По отношению к конфликтной ситуации и компромиссу слэ характеризуется конкурирующим типом поведения. Проявляет слабую готовность к компромиссу. Если решается на такой шаг, то только под воздействием чрезвычайных обстоятельств. Психологически люди такого типа настроены на победу любой ценой, иногда даже в ущерб этическим нормам и принятым на себя обязательствам. Пытаясь найти выход, слэ стремится определить слабые и уязвимые места у оппонента, пользуется этой информацией в дальнейшем поведении. Если анализ проведен — готов приступить к активным действиям.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Установка на победу чревата отрицательными последствиями для деловых и личных взаимоотношений. Стремление преобразовать внешнее окружение, изменить позиции других людей часто приводит к разнообразным столкновениям, которые доставляют больше неприятностей и переживаний оппонентам, чем представителям слэ-типа. Последние, как правило, не замечают сложностей взаимоотношений и не чувствительны к аспектам эмоционального характера. Выход из усложнившейся ситуации общения (конфликта) слэ ищут либо в активизации деятельности, направленной на усиление их позиций, либо в отказе от контакта и поиске других деловых партнеров, благосклонно воспринимающих их точку зрения. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ключевые характеристики ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Хорошо решает тактические задачи. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Иерархический лидер деловой направленности. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Способ установления контакта ориентирован на объективные показатели. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Форма деловой активности ориентирована на волевое преодоление преград и препятствий. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 5. Система взаимодействия с подчиненными предполагает административный контроль. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 6. Относится к конкурирующему типу поведения. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 7. Достигает крупных результатов в деловой активности без учета человеческих взаимоотношений. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 8. Склонен к активному отрицанию этики в бизнесе. Считает, что цель оправдывает средства. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 9. Мотивы и цель: эгоизм, карьеристские планы, стремление к жизненному успеху. Озабоченность собственными прибылями и успехами своей фирмы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 10. Отношение к закону: склонен к криминальному риску. При возможности старается преодолеть правовые барьеры. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 11. Стратегия: хорошая ориентация в ситуации, нацеленность на благоприятные возможности для увеличения прибыли. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Условия для работы")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Стимулом, который движет маршалом, является статус: достижение определенной степени служебной лестницы, карьера, возможность дорого и красиво одеваться. Поэтому, если это ваш сотрудник, больше используйте его управленческие способности, поручайте самостоятельные задания, управление другими людьми, ответственность. Если есть возможность, назначьте его на более высокую должность, если нет — то придумайте престижное название для прежней. Помните, что ему гораздо легче командовать другими людьми, чем подчиняться самому. Если нет возможности присвоить ему высокую должность сейчас, подумайте об этом в дальнейшем (и пообещайте, если можно). Если маршал не будет удовлетворен своим статусом в группе, он всегда будет искать другое место, где его влияние на ход дел больше. В деловом общении с маршалом следует учитывать, что он стремится конкурировать с другими людьми и настроен на то, чтобы добиваться преимуществ в любом, даже незначительном деле. Поэтому лучше, если вы заранее подготовитесь к разговору, просчитаете, в каких незначительных вопросах вы согласны уступить преимущество маршалу, а где вы не согласны на компромиссы. Добившись небольших уступок с вашей стороны в начале разговора, маршал станет гораздо более лояльным в конце, когда вы сможете дипломатично уговорить его. Если он мрачнеет и замыкается в себе, нужно осторожно, при помощи тактичных вопросов расспросить о причинах плохого настроения (не упорствуя, если он не хочет отвечать). Чаще всего это может быть неуверенность в завтрашнем дне, своих возможностях или поведении окружающих. Если вы сумеете каким-то образом обнадежить его фразами типа «не печалься, все образуется, ведь...», он станет внутренне симпатизировать вам в дальнейшем. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендации по адаптации (самосовершенствовнию)")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вы — без сомнения, волевой и решительный человек. Всегда ставите перед собой крупные, далеко идущие цели. Не любите зря тратить силы на мелкие, несущественные дела. Вы умеете хорошо и гибко реагировать на ситуации, идете на временные компромиссы, но не теряете из виду главную цель. Ваш деятельный характер особенно хорошо проявляется в критических ситуациях. Ваше упорство в преодолении препятствий возрастает пропорционально их количеству. Вы очень азартны, хотя попусту не рискнете. Вы любите занятия, которые дают возможность проявлять такие качества, как смелость, находчивость, сила воли и хладнокровие в момент опасности. Вы обладаете хорошими организаторскими способностями. Вы умеете найти подходящих людей и правильно расставить в зависимости от их деловых качеств и функциональной полезности, умеете направить их усилия на достижение главной цели. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вы не стремитесь выставлять свои заслуги напоказ. Вы предпочитаете оставаться в тени, стимулируя других людей к нужной вам деятельности. Для вас характерно чувство ответственности за порученное дело. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Основная проблема вашей жизни — недостаток такта и дипломатичности в отношениях с людьми. Иногда вы открыто проявляете нетерпимость к чужим недостаткам и слабостям. Для достижения своих целей вы порой способны переступить через интересы других людей, чем можете вызвать моральное осуждение окружающих в свой адрес. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Помните, что формула «цель оправдывает средства» не всегда бывает верна. Воспитывайте в себе терпимость, уважайте достоинство и чувство других людей. Глубокое изучение философии и религии, где обосновываются общечеловеческие ценности, поможет вам глубже разобраться в сущности человека. Воспитывайте в себе вежливость и корректные манеры поведения. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Больше уважения проявляйте к лицам противоположного пола. Не теряйте чувства романтики, без которого ваша жизнь может превратиться в бесцветное существование. Не ждите от своего партнера исключительной инициативы в проявлении чувств, старайтесь чаще демонстрировать ему свое расположение.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Другая ваша проблема — чрезмерная концентрация внимания на недостатках в человеке. Это может послужить причиной несправедливого к нему отношения.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Будьте более чутки и внимательны, заботьтесь о людях не только на словах, но и на деле. Не оставляйте без ответа обращенные к вам вопросы. Выслушивая человека, не думайте о постороннем, сконцентрируйтесь на его проблемах. Не преуменьшайте роль интеллектуального труда на производстве. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Чаще задумывайтесь о возможных последствиях своих поступков. Никогда не перекладывайте ответственность за просчеты в деле на других людей — это может отразиться на вашей репутации в будущем. Пытайтесь контролировать те ситуации, которые выходят за рамки формального общения. Вы можете поставить себя в неловкое положение неуместными вопросами, которые могут быть восприняты окружающими как бестактные. ")])])])}
var staticRenderFns = []

export { render, staticRenderFns }