var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',[_vm._v("INFP. Интуитивно-этический интроверт. Лирик")]),_c('h2',[_vm._v("Общее описание")]),_vm._v(" 1. Мечтательный романтик и фантазер. Любопытен: неравнодушен ко всему новому, необычному, красивому. Настойчив в просьбах, если им движет сильное желание. Умеет улучить момент, когда можно подойти к человеку. Свободно обращается со своим и чужим временем. 2. Эмоционален и обаятелен. Улыбается, даже когда говорит неприятные вещи. Производит впечатление неожиданными, порой экстравагантными поступками и высказываниями. Обладает тонким чувством юмора. Умеет утешить человека, поднять настроение. 3. Внешний вид — источник его воздействия на людей. Если надо, одевается элегантно, порой даже изысканно. В расслабленном состоянии жалуется на плохую жизнь, страдания. Любит, чтобы за ним ухаживали, выслушивали жалобы о плохом самочувствии. Ему трудно отказаться от привычного комфорта. 4. Без твердого руководства неорганизован и несобран. Не может долго сосредоточивать свое внимание на предмете труда. В обращении с деньгами неэкономичен и капризен: готов потратить последние деньги на покупку «для души». Склонен давать непродуманные обещания. Веселится, чтобы заглушить тревожные предчувствия. "),_c('h2',[_vm._v("Подчинённый. ")]),_c('h3',[_vm._v("Сильные стороны.")]),_vm._v(" Эмоционален, обаятелен. Мягкий, тактичный человек. Обладает утонченностью в восприятии мира. Часто имеет развитые гуманитарные интересы. Чуток и внимателен к людям, старается со всеми поддерживать ровные отношения. Проявляет терпимость к недостаткам и слабостям других. Романтичен и оптимистичен, верит в светлое будущее. Компромиссен: умеет терпеливо ждать и прощать. Хорошо развито предчувствие, тревожится за исход дела, хотя внешне этого не показывает. Уважает сильных авторитетных людей, которые могут выдать конкретные указания. В интересах дела может проявлять хорошие дипломатические способности, располагая людей к себе своей скромностью и обаянием. Обладает неплохим чувством юмора. Искусен в умении нравиться. К каждому умеет найти индивидуальный подход. Демонстрирует изысканность, хорошие манеры, грациозен в движениях. Со вкусом одевается, элегантен. "),_c('h3',[_vm._v("Проблемы. ")]),_vm._v(" Внутренне раним, противоречив. Несколько неуверен в себе, склонен к сомнениям и колебаниям в выборе решения. Его нужно вовлекать в дело. Нуждается к моральной, физической и материальной поддержке. Из-за рассеянности иногда забывает выполнять обещания. В работе склонен утопать в деталях, отвлекаться на мелочи и при этом не успевать сделать в срок более важные дела. Не любит строгих порядков, бездумного подчинения регламентам и инструкциям. Ему с трудом даются экономия и трезвый расчет в делах. На рабочем месте и в быту может допускать «художественный» беспорядок. Не любит людей, которые скупятся в мелочах. Болезненно воспринимает критику своей работы. Охотно делится своими проблемами с окружающими, ищет понимания, сочувствия и поддержки. Его работоспособность имеет явно выраженные периоды спадов и подъемов. От него нельзя требовать и ожидать: • постоянно высокой работоспособности • пунктуальности и организованности • быстрой конкретной отдачи в делах • умения хорошо анализировать ситуацию • умения руководить большим коллективом, распределять обязанности. Рекомендуемый род деятельности. Лучше всего, если работа лирика носит гуманитарную направленность. Он может хорошо работать там, где нужно элегантно общаться с не очень большим количеством людей. Из лириков получаются неплохие секретари (особенно под началом у маршалов), переводчики, библиотекари, литераторы, журналисты, редакторы текстов. Они также хорошо могут себя реализовать в сфере искусства (в качестве театральных критиков, поэтов). Руководитель Настоящий лирик обладает сентиментальной чувствительностью. Он долго помнит свои впечатления, радости и огорчения, часто к ним возвращается. Он редко живет настоящим, хорошо помнит прошлое, думает о будущем. Верен своим привычкам, не любит перемен. Часто нерешителен и непрактичен, требует к себе мягкого, доброжелательного отношения, замыкается в себе, если с ним грубо обращаются. «Лирик» — руководитель-утешитель Представитель этого социотипа умеет установить эмоциональные связи с членами коллектива. Проявляет к ним уважительное отношение, вежливость, предупредительность. Демонстрирует понимание проблем, сопереживание, оказывает поддержку, чаще словесную. Не жалеет времени, чтобы выслушать человека. Вселяет оптимизм, помогает поверить в свои способности. «Лирик» старается избегать единоличного решения о принятии непопулярных мер. Предпочитает разделить ответственность с другими, распределить ее так, чтобы она стала практически неуловимой. Этот тип руководителя нельзя отнести ни к фронтальным, ни к иерархическим лидерам. Однако он лучше себя чувствует в иерархически организованной системе, в которой каждому отведено свое место. Склонен к скрытым формам контроля. Связанно это с тем, что психика «лирика» пронизана предчувствиями. Он хорошо помнит прошлый опыт, стремится не повторять ошибок. Это держит его в некотором напряжении, заставляя искать источник опасности. Соответственно форма деловой активности ориентирована не на преодоление преград, а на предусмотрительность. «лирику» лучше удается решать задачи стратегического характера. Хорошо чувствует, как должны развиваться события в соответствии с законами реальной жизни. Отрицательно относится к людям, пытающимися культивировать консервативные методы. Обладает инстинктивным стремлением развивать и реализовывать выдвинутые кем-то идеи, а не гасить их и отвергать. В конфликтной ситуации склонен пойти на компромисс. Всякая ситуация воспринимается им в ее противоречивых тенденциях, поэтому может колебаться в выборе окончательного решения. Часто не улавливает перевес одной из борющихся сил, тенденции данного момента. Способен быстро менять свое мнение на противоположное, если соотношение сил явно изменилось. В споре двух партнеров ему трудно отдать кому-либо предпочтение, до тех пор пока перевес одной из сторон не станет явным. Ориентируется на несколько вариантов развития какого-либо события. Один из них, как правило реализуется. Ключевые характеристики. 1. Хорошо решает стратегические задачи. 2. Эмоциональный лидер в иерархически организованной системе. 3. Способ установления контакта ориентирован на человеческий фактор. 4. Форма деловой активности ориентирована на предвидение и заблаговременную предусмотрительность. 5. Система взаимодействия с подчиненными предполагает эмоциональный контроль и использование эмоций в качестве инструмента воздействия на людей. 6. Характеризуется компромиссным типом поведения. 7. Ценит хорошие отношения выше производственных результатов. 8. Отношение к этическим нормам зависит от симпатий, антипатий, степени угрозы его статусу. Считает, что управленческие решения могут опираться на разные «правила игры». 9. Мотивы часто эгоистические. Цель - прибыль в рамках закона. 10. Отношение к закону: предпочитает следовать букве закона. 11. Стратегия: личную мораль может использовать по своему усмотрению. Условия для работы Стимулом для лирика является возможность спокойно и самодостаточно работать в «своем» темпе. Он любознателен, и если работа способствует его саморазвитию, интересна лично для него, он способен трудиться с большей отдачей. При этом лучше не обращать внимания на то, что его работоспособность неравномерна: в периоды подъемов он наверстает то, чего не сделал раньше. Лирик способен долгое время трудиться на перспективу, даже если не получает хорошего материального поощрения сейчас. Если он надеется на хорошие возможности в дальнейшем, вы можете располагать его поддержкой. В силу высокой интуитивности лирики часто бывают наивны, непрактичны, нереалистичны. Им бывает трудно устроить налаженный быт, обустроить рабочее место, а также постоять за себя в случае необходимости. Поэтому, если вы окажете ему посильную поддержку и он решит, что вы — человек надежный и решительный и на вас можно полагаться, он может стать одним из ваших постоянных сотрудников: в силу интровертности лирики не склонны часто менять место работы. Лирики романтичны, аристократичны, обладают хорошим чувством юмора. Поэтому, если вы способны иронично шутить, а также изредка преподносить лирику небольшие, но изысканные подарки, — вы найдете у него максимальное понимание и поддержку. Рекомендации по адаптации (самосовершенствованию) Ваша сильная сторона — развитое интуитивное предчувствие, которое позволяет вам извлекать уроки из ошибок прошлого, чтобы обезопасить свое будущее. Будущее вы представляете себе оптимистически. Вы — мечтательная, романтическая натура. Вам по душе красота в искусстве и в жизни. Ваша творческая натура не выносит серости и стандарта. В любое дело вы вносите элементы изящества и оригинальности. Вы хорошо разбираетесь в настроениях людей, обладаете тонким чувством юмора, умеете с его помощью улучшить настроение себе и своим близким. Вы обладаете богатым миром разнообразных чувств и во многом зависите от нюансов своего душевного состояния. Вы способны на возвышенные чувства и склонны идеализировать людей. В окружающий мир вы вносите праздничность и возвышенность. Ваша главная проблема состоит в том, что вам трудно постоянно заставлять себя заниматься повседневными, неинтересными делами, которые носят сугубо материальный характер. У вас не получается экономного ведения хозяйства, вам порой трудно устоять перед соблазном приобретения красивых вещей, стоимость которых выходит за пределы вашего бюджета. Следите за тем, чтобы не тратить деньги на ненужные безделушки: вы подвергаетесь риску пострадать в финансовом отношении. Проявляйте больше сдержанности в тратах, чтобы не поставить себя в унизительное положение, когда вам приходится рассчитываться с долгами самыми разными способами, вплоть до продажи вещей. Не идите на поводу у своих честолюбивых устремлений и иллюзий в отношении «легкой» жизни, не подчиняйте им соображения этики и морали, чтобы избавиться от повседневных забот. Пусть ваши искренние чувства подскажут вам правильный выбор между материальным и духовным аспектами жизни. Другая ваша проблема состоит в том, что вы не склонны долго скрывать от окружающих свои переживания и физические недуги. Этим вы можете утомить людей и вызвать у них недоверие к искренности ваших переживаний. Воздерживайтесь от излишних претензий к окружающим и жалоб на отсутствие должной заботы по отношению к вам. Чтобы заслужить внимание близких, старайтесь умерить некоторую капризность в вашем характере. Проявляйте больше великодушия и деятельной заботы о людях. Старайтесь заслужить хорошее отношение к себе трудолюбием и активным участием в общих делах и ни в коем случае не давайте пустых обещаний. Не проводите свободное время в мечтаниях, бесплодных эмоциональных беседах и тайных переживаниях. Если вы большую часть свободного времени посвятите реальному делу, то ваши шансы преуспеть в жизни значительно возрастут. ")])])}
