var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',[_vm._v("ENTP. Интуитивно-логический экстраверт. Искатель")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общее описание")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Хорошо видит далекие перспективы. Большой выдумщик: у него всегда много новых идей и проектов. Интересуется всем подряд в надежде отыскать что-то уникальное. К прошлым достижениям относится пренебрежительно. Занимается тем, что ему интересно, а не тем, что гарантирует реальную отдачу. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Много размышляет внутри себя, отыскивая закономерности в явлениях. Довольно рассеян — то и дело теряет мелкие предметы. Его логика другим кажется парадоксальной. Не боится пойти против устоявшегося мнения, если считает его устаревшим. Стремится совершить крупное изобретение или открытие. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Уступчив в вопросах быта и повседневных потребностей. Попыткам подчинить себя строгому распорядку оказывает активный отпор. Несмотря на то, что занят поисками альтернатив скучной повседневности в трудных житейских ситуациях проявляет смекалку и расторопность. С ним нельзя разговаривать языком силы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Плохо чувствует насколько близко можно сойтись с человеком. Со всеми старается быть одинаково дружелюбным. Добивается, чтобы собеседник выражал заинтересованное отношение к его информации. В общении доступен и демократичен. Нуждается в периодических эмоциональных встрясках, так как в рутине его настроение падает. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Подчинённый. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильные стороны.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Хорошо развито познавательное мышление. Эрудирован, любознателен, много читает и запоминает. Обладает развитой интуицией, помогающей выбрать правильное решение. Хорошо видит перспективы идей и дел. Может найти неожиданное решение проблемы там, где не видят его другие, на основе анализа большого объема разнообразной информации и рассмотрения ее как бы со стороны, интуитивно. Охотно советует, как выпутаться из сложных ситуаций. Расплывчатые идеи способен переработать в теорию, имеющую форму системы. Структуралист и классификатор, идущий от общего к частному. Довольно напорист, нередко обладает организаторскими способностями в области поиска принципиально новых подходов. Часто работу ставит выше семейных или личных отношений. Демократичен, не любит чинопочитания. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проблемы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Не переносит строгую регламентацию и монотонный режим работы. Не может жить по строгому плану. Довольно неорганизован: отвлекаясь на интересные детали, иногда не успевает сделать главное к намеченному сроку. Может говорить не по теме, уходя в свои мысли. Ему с трудом удается порядок в делах, на рабочем месте. Может допускать погрешности в оформлении деловой документации. Не всегда следит за внешним видом. Бывает наивным, слишком доверчивым. Из-за предприимчивости, с одной стороны, и неумения идти на компромиссы — с другой, ему с трудом удаются бесконфликтные отношения с людьми. Бывает неуступчивым, даже агрессивным, особенно если на него давить. Может с апломбом доказывать свою правоту, если убежден в ней. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("От него нельзя требовать и ожидать: ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • практичности замыслов ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • пунктуальности и исполнительности ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • последовательности и завершенности ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • постоянного порядка в быту и на рабочем месте ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • качественного выполнения рутинной работы ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • умения мягко подстраиваться к 