одителя воспринимаются как приказ. Руководитель-творец агитирует за новые направления и методы в любом деле. Рутинные вопросы, в том числе контроль и инструктирование, выходят за пределы его внимания. Главное — обрести сторонников, которые взялись бы за практическую реализацию идеи. Отсутствие контроля компенсирует делегированием полномочий и ответственности. Стремится достичь высоких производственных результатов путем мотивации групповой работы, установления приоритетов в решении задач, побуждения к самостоятельному анализу, распознаванию существенных факторов работы на перспективу. Такое руководство на практике ведет к разрушению старых организационных структур и созданию новых, приспособленных к изменяющимся условиям или к решению других задач. Поэтому этот стиль руководства далеко не всегда и не везде пригоден на производстве, более приемлем для научной деятельности и вовсе не годится для армии. Склонен коллегиально решать вопросы, давая возможность высказаться каждому. Однако, если аргументация противоположной точки зрения покажется неубедительной, приложит все силы, чтобы доказать обратное. Стремится сотрудничать с теми, кто поддерживает его идейные позиции, разделяет взгляды на будущее. В случае необходимости способен конкурировать с другими: успешнее в сфере идей, требующего полемического накала, менее успешно — в сугубо организационных вопросах. Во взаимоотношениях стремится быть объективным, придерживаться принципов справедливости. В некоторых случаях может проявить неадекватную реакцию в споре. Быстро успокаивается, если удостоверится, что в противодействии нет личной подоплеки. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ключевые характеристики.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Хорошо решает стратегические задачи. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Адекватно может себя реализовать в роли фронтального лидера. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. В установлении делового контакта ориентируется на объективные критерии. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Форма деловой активности ориентирована на видение перспективы и преодоление препятствий для ее достижения. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 5. Система взаимодействия с подчиненными строится в соответствии с принципами демократизма и коллегиальности, ориентирована на самодеятельность и инициативу. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 6. Сотрудничающий тип поведения с элементами конкурирующего типа. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 7. Достигает крупных результатов за счет хорошей ориентации в потенциальных возможностях работников. Эмоциональный срез не имеет для него существенного значения. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 8. Придерживается этических принципов в бизнесе. Может ориентироваться на разные «правила игры». ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 9. Мотивы и цель: большое значение имеет мотив престижности, удовлетворение интеллектуальных потребностей. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 10. Отношение к закону: стремление действовать в рамках закона, который может быть истолкован довольно широко. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 11. Стратегия: полная свобода управления, отрицание догм. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Условия для работы.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Стимулом для искателя является стремление к уникальности. Поэтому необходимо, чтобы работа была интересной, давала возможность личностного роста, саморазвития. Еще лучше, если это будут поисковые, единственные в своем роде разработки. Поручая такого рода работу искателю, необходимо донести до него, что только он благодаря своим уму, эрудиции, прозорливости, уникальности может ее выполнить. После выполнения работы похвалить в том же ключе, отметить его неординарные способности. Постараться поскорее подобрать новую, еще более интересную работу, так как с потерей новизны искатель быстро теряет интерес к делу. Если он чего-то не довел до конца, сделал не так, как было намечено, или некачественно (часто это конкретная, рутинная часть работы), лучше всего «закрыть на это глаза», поручить доводку и оформление другому специалисту. Сам искатель не умеет постоянно следить за своим внешним видом, здоровьем. Это проходит как бы мимо его внимания. Хорошо, если кто-то другой заботится об устройстве его рабочего места, проявляет внимание к внутреннему состоянию искателя и помогает его улучшить, это его расслабляет, снимает возбуждение и напряженность. Активизировать искателя могут: проявляемый по отношению к нему мягкий юмор, подшучивание, ненавязчивые вопросы, имеющие целью привлечь внимание к самому себе. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендации по адаптации (самосовершенствованию)")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вашей сильной стороной, безусловно, является хорошо развитое познавательное мышление. Вам в голову часто приходят интересные, перспективные идеи. Вы умеете проникнуть умом в глубь событий и предвидеть перспективу их развития. Ваша развитая интуиция помогает вам в выборе правильных решений. Вы способны заглянуть в саму суть вещей и явлений. Вы много читаете и интересуетесь всем новым, необычным. Однако в этом кроется опасность «распотрошить» свои силы, потерять основной интерес, определяющий вас как личность. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Инструментом для реализации ваших интуитивных идей и догадок служит логика. Вы стремитесь логически обосновать свои идеи и убедить тем самым окружающих людей в их правильности. Благодаря этому качеству вокруг вас объединяются ваши единомышленники. Вы — враг всего косного и устаревшего. В этом таится опасность стать на позицию тотального нигилизма. Ваша основная проблема — это неумение правильно разобраться в отношениях между людьми. Особенно трудно для вас — осознать истинное отношение к вам окружающих. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("После того как вы интуитивно разобрались в человеке, он становится вам неинтересен. Вы не знаете, что с ним делать дальше. Это может восстановить его против вас. Вы также не видите своих истинных недоброжелателей. Не спешите делать выводы относительно оценки окружающих вас людей. Старайтесь определиться в этом вопросе по их объективным поступкам. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вам трудно даются стабильные хорошие отношения с близкими. Вы можете нечаянно проявить бестактность, нарушить устоявшуюся гармонию отношений. Сдерживайте сиюминутную реакцию на тот или иной поступок близкого человека. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вам полезны также следующие рекомендации. Не увлекайтесь излишним демократизмом, который может незаметно для вас перейти в фамильярность. Это может способствовать подрыву вашего авторитета среди людей. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проявляйте в своем поведении больше такта, чтобы не шокировать людей своей откровенностью и прямолинейностью. Этим вы можете нажить недоброжелателей, даже сами того не желая. Еще одна из ваших проблем — это неумение быть постоянно собранным, слабая самодисциплина. Из-за этого вы запускаете свои повседневные дела, следствием чего является срыв намеченных мероприятий или установленного графика работы. Делайте все вовремя. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Если вы являетесь начальником, то обратите внимание на то, чтобы не скатываться к волюнтаризму. Избегайте необоснованного усиления давления на сотрудников, не перегибайте палку в требовательности и волевом нажиме. Такая неадекватная реакция может вас поставить в смешное положение. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Переключайте свою потенциальную волевую энергию на занятия спортом, чтобы давать ей выход. Это повысит ваш жизненный тонус, выработает выдержку. Вы станете более собранным, дисциплинированным.")])])])}
