var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',{attrs:{"align":"left"}},[_vm._v(" ESTJ. Логико-сенсорный рациональный экстраверт. Администратор")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общее описание")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Напорист и работоспособен. Большой труженик, не может сидеть без дела. Борется против хаоса и беспорядка, где бы ни находился. У него сильно развито чувство ответственности. Словам всегда предпочитает дела. Не терпит хитрости и коварства, свое мнение высказывает прямо. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Превыше всего ценит добротность и качество. Всегда приходит на помощь, когда людям угрожает физическая опасность. Действует в такой ситуации смело и решительно. Очень заботлив, обеспечивает своим близким материальный достаток. Если отдыхает, то так же основательно, как и работает. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. В деловых отношениях сух и официален, но в неформальной обстановке стремится проявить радушие и юмор. Однако ему недостает дипломатичности в отношениях — может разнервничаться, вспылить. Упрямый спорщик очень горячится, когда доказывает свою правоту. Комплиментов делать не умеет. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Застревая в деталях работы, может не укладываться в срок, что очень болезненно переживает. Не готов к неожиданностям. Не станет болтать по пустякам, хотя ему трудно прервать телефонный разговор. Очень не любит, когда его отвлекают от работы. Консервативный сторонник традиций, с трудом воспринимает новые веяния и моды. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Подчинённый. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильные стороны.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Деятельный и напористый человек. Умеет трезво оценить ситуацию и принять практическое решение. Хороший администратор: отлаживает качественный рабочий процесс. Старается создать все условия для слаженной работы. Способен координировать деятельность разных людей и подразделений. Очень прагматичен: поступки людей оценивает с точки зрения их пользы. Любит во всем надежность и основательность. Отличатся высокой работоспособностью. Умеет бережно и рационально обращаться с деньгами. Практичность и добротность вещей предпочитает моде. Любит во всем порядок. Обладает развитым чувством долга. Ценит время и не любит тратить его попусту. Очень пунктуален, старается все планировать наперед. Предпочитает зря не рисковать. Хороший хозяин в доме. Расторопен и изобретателен в бытовых вопросах. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проблемы ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Консервативен в своих вкусах и привычках. Не любит признавать свои ошибки и идти на компромиссы. Плохо воспринимает критику, даже в шутливой форме. Ему не хватает дипломатичности в отношениях. Бывает излишне резок в своих критических оценках. Осуждает всех, кто, по его мнению, недостаточно качественно и честно работает. Избегает делать комплименты, хвалить за хорошую работу — считает ее нормой. Не любит говорить о развлечениях, сторонник строгого воспитания. Если его работу критикуют некомпетентные люди. Может вспылить. С близкими нередко проявляет властность и требовательность. Стрессонеустойчив, особенно его выбивают из колеи ситуации неопределенности. Тяготится долгим ожиданием какого-либо события. Утопая в деталях и отвлекаясь на другие дела, может не успевать закончить работу к сроку. Очень болезненно это переживает. Не имея достаточного количества фактов, откладывает принятие решения. Не выносит, когда его подгоняют или отвлекают от работы. Придерживаясь установленных правил поведения, требует того же от окружающих. Вместе с тем, может неожиданно разнервничаться, сорваться, проявить резкость и нетерпимость. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("От него нельзя требовать и ожидать:")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • гибкого изменения своего поведения соответственно ситуации ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • мягкости и дипломатичности в отношениях с людьми ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • выдержки и хладнокровия в экстремальных ситуациях ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • генерации нестандартных идей. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендуемый род деятельности: ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Управленческая сфера, где необходимо руководить другими людьми и координировать их деятельность (при условии стабильности ситуации и законов); экономисты, работники кредитных отделов банков; технические секретари с распорядительскими функциями в фирмах; сопровождение технического процесса (ремонт и эксплуатация техники); строительство; сельское хозяйство, фермерство. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Руководитель. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Любит порядок, твердо стоит на своих жизненных позициях, трудолюбив, требователен к себе и другим. Часто для таких людей работа — это жизненная страсть, которой он отдает много сил и энергии. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("«Администратор» — руководитель-организатор. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Хороший организатор, обладает качествами практической деловой направленности. Умеет выдержать линию, намеченную ранее, устранить барьеры, мешающие работать, привлечь подчиненных к активному, творческому труду. Будучи руководителем, часто выдвигает новые предложения по более рациональному использованию материальных и интеллектуальных ресурсов. Очень активен в указаниях, инструктировании и надзоре за работой. Отдает приоритет общению. «администратор» тратит много сил на знакомство с людьми и развитие контакта с ними. В работе ориентируется на предыдущий положительный опыт. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Склонен решать тактические задачи, это объясняется своеобразием его мышления, направленного на вычленение логики фактов, событий, поступков. Из всего потока информации выделяет то, что может быть полезным для планируемой деловой активности, остальное отбрасывает как не имеющее отношения к делу. Такая установка позволяет рационально относится к имеющимся в данной системе материальным и интеллектуальным ресурсам. Составляет планы на постепенное, эволюционное развитие этой системы, рационализируя и совершенствуя отдельные ее элементы. Склонен придерживаться традиционных ценностей, включая деловую этику и уважение к другим людям. С помощью перечисленных качеств добивается значительных успехов в стабильных структурах, с трудом управляет сложными, изменчивыми организационными системами. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Благодаря деловой направленности мышления лсэ проявляет способности администратора. Тщателен и аккуратен. Хорошо работает с деловой документацией. Ответственно относится к поручениям. При необходимости остается после работы без указания «сверху». На деловые встречи, как правило, не опаздывает. Очень не любит невыполненных обещаний.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Требовательность к подчиненным совмещается со стремлением создать им комфортные условия для работы, устранить внешние препятствия для их эффективной деятельности. Только в этом случае считает, что имеет право на требовательность, чем выгодно отличается от типов слэ и лси, которые мало интересуются условиями работы подчиненных. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Лучше справляется с функциями иерархического лидера. Опасается риска. Консервативен, привержен к испытанным методам и формам работы. Новые цели пытается достигать апробированными методами, используя старые связи и контакты. Негативно воспринимает критику по данному вопросу. Взаимодействие с окружающими людьми и деловыми партнерами организует на основе кодекса чести, правил честной игры. Ярый противник обмана, лжи, двуличия. Его честность базируется на логических принципах. В межличностных отношениях характеризуется эмоциональной невовлеченностью, т.е. Не испытывает потребности вступать в близкие отношения со множеством других людей. В системе формальных связей для него более характерно ровное общение. Несмотря на это тяготеет к тому, чтобы влиять на окружающих, контролировать их мысли и поступки. Старается быть в гуще событий, иметь широкий круг знакомств, которые служат для него источником разнообразной информации. Тяготеет к сотрудничеству. Старается согласовывать свои интересы с интересами партнера. Тем не менее поиск компромисса для него труден. По каждому вопросу он имеет свое особое мнение. Готов спорить на любые темы, даже если не вполне компетентен в них. Во время переговоров может горячится, упорно доказывать свою правоту. Склонен преувеличивать отрицательные последствия и недочеты какого-либо мероприятия. Дорожит своей репутацией. Именно на этой основе возможен компромисс с представителем лсэ. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Администратор по своим психологическим качествам способен выступать в роли лидера-организатора. Он понимает, в чем состоят интересы тех людей, с которыми он связан по работе; способен понять трудности (проблемы) отдельного человека. Готов отстаивать интересы людей, обладает развитой способностью понимать и ясно выражать общие интересы коллектива. Может вступится за человека, если с ним обходятся несправедливо. Ставит вопрос о нуждах коллектива перед руководством, не дожидаясь приказа «сверху». Способен понимать то, о чем люди предпочитают умалчивать, а также улавливать и выражать общее мнение членов коллектива по любым значимым для них вопросам. Готов жертвовать личными интересами ради интересов коллектива. Может, независимо от занимаемой должности, принимать на себя функции представителя коллектива. Лидер-администратор настойчив в отстаивании интересов своих коллег. Способен вовлекать людей в обсуждение общих проблем коллектива, выступает с предложениями о путях удовлетворения нужд подчиненных. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ключевые характеристики.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Хорошо решает тактические задачи. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Деловой лидер в иерархически организованной системе. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. При установлении контакта ориентирован на объективные показатели. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Деловая активность направлена на установление препятствий и дискомфорта в производственной деятельности подчиненных. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 5. Система взаимодействия с подчиненными предполагает контроль и выдачу указаний. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 6. Характеризуется как сотрудничающий тип поведения с элементами конкурирующего стиля. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 7. Результаты производства ценит выше сложившихся отношений. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 8. Отношение к этическим нормам: считает, что управленческие решения должны опираться на четкие законы и принятые \"правила игры». ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 9. Мотивы и цель: благие намерения, однако воздействие принимаемых решений на других людей в расчет не принимает. Цель — прибыль в рамках закона. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 10. Отношение к закону: предпочитает следовать букве закона. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 11. Стратегия: полная свобода управления в рамках закона. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Условия для работы.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Стимулом, движущим администратором, является достижение высокого управленческого статуса в группе. Короче говоря, он стремится распоряжаться всеми делами по-своему и любит, когда с его мнением считаются. Проще всего, конечно, определить его на одну из руководящих должностей — у вас не будет проблем ни с подразделением, которым он руководит, ни с самим администратором. Потому что если это рядовой сотрудник коллектива и его управленческие способности не оценены по достоинству и не нашли своего применения, он все равно будет вмешиваться в управленческие дела, даже не входящие в его компетенцию: будет советовать вам, как лучше поступить, а также критиковать ваши неправильные действия. Со стороны может показаться, что у столь практичного типа не должно быть никаких проблем в жизни. Однако, несмотря на всю свою организованность, деловитость и рациональность, администратор редко бывает счастливым и удовлетворенным. Ему с трудом дается душевное равновесие и оптимистический взгляд на жизнь. Ему не дает возможности отдыхать вечная озабоченность текущими делами и завтрашним днем. Ему не приносят удовлетворения родные и близкие, так как они практически никогда не обладают такой добросовестностью и ответственностью в делах, какую бы хотел в них видеть администратор. Можно сказать, что ему мешает жить «комплекс вечного отличника», стремящегося получать в жизни только «пятерки». Поэтому единственное, что может несколько облегчить его участь \"работающего за весь социум\" — это просто душевное участие, спокойное доброжелательное выслушивание его переживаний, сочувствие. Так как администратор — тип напористый. Совершенно бессмысленно переубеждать его, когда он вошел в возбужденное негативное состояние. Нужно просто переждать этот напор, дав администратору выговориться. А когда он уже начнет успокаиваться, его нужно душевно поуговаривать, сказать, что не все так плохо, что такое случалось и раньше, но кончилось благополучно и т. П. После того как он окончательно успокоится, его можно и покритиковать, объяснив, что он сам был не прав и вел себя резко, что не все люди плохие и не всегда. Напомните ему конкретные ситуации, когда люди, о которых он плохо отзывался, были компетентны и вели себя безупречно. Администратор нуждается не только в душевной поддержке, но и постоянном ненавязчивом воспитании, предостерегающем его от резких проявлений по отношению к окружающим. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендации по адаптации (самосовершенствованию)")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вашей сильной стороной является строгая логика фактов, позволяющая вам точно просчитать практическую сторону любого дела. Вы — трезвый реалист, способный правильно оценить полезность и эффективность затеваемого мероприятия. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("У вас хорошая смекалка и отличная информированность в той области, которой вы занимаетесь. Вы — активный и деятельный человек, не любите попусту тратить время, всегда знаете, чего хотите, и умеете этого добиваться трудолюбием и упорством. Вы умеете рационально и экономно использовать материальные ресурсы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Для вас также характерно умение создавать себе и близким материальный достаток и комфорт, вы стремитесь к обеспеченной, здоровой, гармоничной жизни и не жалеете для этого своих сил, а также стимулируете окружающих к различной полезной деятельности. Вы обладаете хорошими организаторскими способностями, так как умеете создать людям условия для работы, заинтересовать их и только потом потребовать отдачи. Вы очень цените качество работы и не переносите недисциплинированности и лени. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вы любите красивые, добротные вещи, вкусную и здоровую пищу. Вам нравится, когда окружающие оценивают вас по заслугам и не посягают на вашу независимость в выборе решений или методов работы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вашей слабой стороной является неумение планировать альтернативные мероприятия на длительные сроки. Вы часто оказываетесь перегруженным неизвестно откуда появляющейся работой, нуждаетесь в заблаговременной подготовке к важным мероприятиям. Вам иногда трудно предвидеть назревающую опасность или неудачу в деле. Неожиданные повороты событий, которые вы не смогли предусмотреть, раздражают вас и даже выводят из равновесия. Вам трудно долго ждать исхода затеваемого дела, особенно если нет уверенности в е