var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('section',[_c('h1',{attrs:{"align":"left"}},[_vm._v(" ENFJ. Этико-интуитивный экстраверт. Наставник ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Общее описание")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Обладает глубокой эмоциональностью, тонко чувствует состояние других людей. Романтик в чувствах. Артистичен и возвышен. Легко управляет внешней экспрессией, переходя от драматизма к жизнеутверждению. Настроен исключительно на вежливое общение, без грубости и понуканий. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Предупреждает людей о грозящей опасности. Хорошо предчувствуя тревожную ситуацию, готовится к ней заранее — продумывает запасные варианты. Интересуется темами, связанными с загадочными явлениями. Любит уединяться и размышлять о смысле жизни, прошлом и будущем. Все проблемы рассматривает с глобальной точки зрения. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Вовлекшись в конкретное дело, забывает о еде и отдыхе. Берется за трудновыполнимые задачи, хотя тяжело переживает любую неудачу. Его цель мобилизовать людей на дело, как же действовать конкретно должны определять другие. Склонен к накоплению и систематизации материалов в интересующей его области. Однако к проработанному возвращается редко. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Его терзают сомнения и колебания, касающиеся текущих проблем жизнеобеспечения. Плохо следит за своим здоровьем, с трудом избавляется от вредных привычек. Недоверчив к комплиментам. Одевается либо подчеркнуто демократично, либо совсем просто. В питании склонен к сочетанию продуктов с противоположными вкусами. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Подчинённый.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Сильные стороны.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Эмоционален, тонко чувствует настроение других людей. Умеет красиво выражать свои чувства: от возвышенных интонаций до тонкой иронии. Может увлечь людей своими эмоциями, повести за собой. Тревожится об опасности, которая таится в развитии ситуации, умеет предпринять необходимые меры, чтобы ее избежать. Предусмотрителен и дальновиден, умеет правильно распределить мероприятия во времени. Верит в неограниченные возможности и способности человека. Старается понять мотивы поведения людей, стоит на моральных позициях воспитателя и наставника.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Пунктуален, имеет развитое чувство ответственности. Обычно имеет широкие гуманитарные интересы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Проблемы")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ему с трудом дается внутреннее равновесие. Склонен излишне драматизировать события. Самолюбив, раним, настроен на корректное, вежливое отношение к себе, долго помнит обиды. Не любит, когда его прерывают, торопят. Остро переживает свои неудачи и разочарования. Категоричен в суждениях и выводах, не склонен к компромиссам. По натуре аристократичен, может допускать крайности в одежде, самовыражаясь таким образом (экстравагантность или, наоборот, элементы небрежности). Не любит делать много дел сразу. Недоверчив к непроверенной информации. Критику своей внешности и привычек в быту переносит болезненно. Может допускать небрежность в оформлении деловой документации. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("От него нельзя требовать и ожидать:")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • неизменного оптимизма ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • быстрой конкретной отдачи в делах ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • трезвого, объективного, безэмоционального взгляда на вещи ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • умения достоверно анализировать ситуацию ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" • демократичности и простоты в общении.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендуемый род деятельности.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Гуманитарная сфера, где нужно пропагандировать какую-либо идею, мировоззрение, религию. Может быть хорошим агентом по сбыту или страхованию, где надо эмоционально вовлечь клиента. Прекрасно работает в сфере искусства, где нужно играть, выражать эмоции (театр, эстрада, ораторское искусство). ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Руководитель")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Представитель этого социотипа живет напряженной внутренней борьбой эмоций, которые время от времени выплескиваются наружу. Для окружающих эти бурные всплески часто бывают неожиданными. Такие люди часто одарены ораторским талантом, способны зажигаться идеей и воодушевлять других. Их влечет фантазия, экзотика, романтика, возвышенные чувства. Своим поведением они демонстрируют условность барьеров, ограничивающих возможности человека. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("«Наставник» — руководитель-вдохновитель")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Вдохновителем может быть только тот человек, который искренне верит в необходимость и полезность избранного дела или исповедуемой идеи, радуется успехам и сопереживает неудачам в ее практической реализации. В этом суть руководителя-вдохновителя. ")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Руководителю такого типа лучше удается решение стратегических задач, так как он хорошо чувствует и учитывает внешнюю ситуацию. Особенно остро воспринимает события, грозящие нарушить нормальный ритм работы, сорвать намеченный план. В таких случаях умеет мобилизовать свои силы и способности. Это выражается не столько в конкретных действиях по устранению угрозы, сколько в призывах исправить положение, в указании на недостаточную работу тех, от кого зависит решение данного вопроса. Делает это эмоционально, часто с использованием этических категорий, вызывая положительный эмоциональный отклик.")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("В зависимости от ситуации может успешно выступить как в роли фронтального, так и в роли иерархического лидера. В первом случае увлекает идеей либо новшеством. Во втором — стремится подчинить организационную структуру поставленной цели. Может проявить склонность к жестким волевым методам давления на подчиненных для наведения надлежащего порядка. При этом предпочитает делать это «чужими руками», используя, например, заместителя. В любом случае выступает сторонником иерархически организованной структуры коллектива. Подсознательно направляет свою деятельность на подчинение интересам коллектива, что является одним из признаков положительного психологического климата. Одновременно это показатель зрелости коллектива, когда лучше удается достичь совместных целей.")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Пользуется разнообразными методами влияния на подчиненных и коллектив в целом. Наиболее приемлемый для него метод — позитивное подстегивание, что может выразиться в улыбке, замечании, позитивной оценке. У подчиненного это вызывает чувство комфорта, активизирует его личную энергию. Однако при необходимости такой социотип успешно может применить метод жесткого обращения. По отношению к некоторым подчиненным это усиливает трудовую мотивацию. Систему взаимодействия с людьми в целом строит на основе отношений, в которых бы учитывалась иерархия власти. Сам с уважением относится к вышестоящему руководству, требует того же от подчиненных.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("При установлении делового контакта ориентируется на личностный фактор. Хорошо чувствует людей, их настроение, эмоциональную направленность. С антипатичным ему партнером предпочитает не иметь дела. Сам придерживается правил корректного поведения, но может отказаться от них в любой момент, если посчитает нужным. В общении стремится создать образ делового, надежного партнера. Хранит верность принятым обязательствам до тех пор, пока считает это полезным для себя или своей организации.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("В случае возникновения конфликтной ситуации демонстрирует бескомпромиссность. Настойчиво защищает свою точку зрения, даже если она противоречит общепринятым нормам. Способен пойти на риск. Добивается цели решительными методами, вовлекая в дело своих сторонников. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ключевые характеристики")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 1. Склонен к решению стратегических задач. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 2. Способен выполнять функции фронтального и иерархического лидера. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 3. Способ установления делового контакта ориентирован на личностный фактор. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 4. Форма деловой активности предполагает предусмотрительность и видение перспективы. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 5. Система взаимодействия с коллегами и подчиненными основана на отношениях иерархии. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 6. Относится к конкурирующему типу поведения с элементами сотрудничающего типа. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 7. Достигает крупных результатов за счет эмоционального воздействия на людей. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 8. Управленческие решения не рассматривает в этической перспективе. Придерживается разных «правил игры». ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 9. Мотивы и цель: благие намерения ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 10. личный успех отождествляет с успехом организации. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 11. Отношение к закону: придерживается закона, если он не мешает реализации поставленной цели. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v(" 12. Стратегия: полная свобода управления. Личная мораль проявляется по усмотрению.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Условия для работы")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Предпочитает творческую свободу, охотно демонстрирует свои способности. Наставником движет стремление к уникальности. Поэтому лучше всего, когда работа ему нравится, дает возможность самовыразиться, проявлять пропагандистские наклонности. Также она должна служить личностному росту, способствовать самосовершенствованию. Поэтому почаще посылайте Наставника на всяческие семинары, симпозиумы, конференции. Он любит самосовершенствоваться, а также может неплохо выступать с докладами. Наставник любит порядок и дисциплину во всем, но самому ему трудно их создавать и поддерживать. Поэтому, если вы будете в общении с ним пунктуальны, корректны и дисциплинированны, это сильно расположит его в вашу пользу. Ему также нравятся люди, которые могут защитить его интересы, уверенно отстоять свое мнение. Однако работает Наставник довольно сумбурно: бывает, что от него долго нельзя дождаться никакого конкретного результата, а он красиво живописует вам все сложности, возникающие при исполнении задания. Он также постарается навязать вам свою стратегию действий и удобный для него режим работы. Ни в коем случае не уступайте: разбейте всю работу на небольшие последовательные этапы, установите сроки проверки и строго требуйте самых доскональных отчетов на каждом «контрольном» пункте. Это мобилизует Наставника и сделает его работу более организованной, ведь он — человек ответственный. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Рекомендации по адаптации (самосовершенствованию)")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Наиболее развитой стороной Вашей личности является способность испытывать сильные чувства и эмоции. Вы — человек со сложным духовным миром, полным контрастов и противоречий. Ради истинных, глубоких чувств Вы можете пожертвовать многим. ")]),_c('h4',{attrs:{"align":"left"}}),_c('h4',{attrs:{"align":"left"}},[_vm._v("Свойственная Вам поэтичность натуры позволяет выразить испытываемые Вами чувства в образной форме, часто драматической, иногда трагикомической. Вы можете обладать талантом оратора, умеющего зажечь речью большое количество людей, даже если Вы об этом и не подозреваете. Вас можно назвать человеком дальновидным и предусмотрительным. Вы хорошо чувствуете назревание кризиса в развитии событий и умеете принять меры заранее, чтобы избежать его. Окружающих Вы также стремитесь предупредить о возможных неприятностях, грозящих им. В этом находит выражение общая гуманитарная направленность Вашей личности. Вы — человек довольно принципиальный, не прощаете несправедливых обид и унижений. Ради этого, все обдумав и взвесив. Вы способны предпринять самые решительные меры. Однако в некоторых случаях Ваша принципиальность может быть негибкой, а решительность чрезмерной. Стремитесь проявлять сдержанность в экстремальных ситуациях, принимать решения в спокойном состоянии. Ваша основная проблема — неумение избегать ситуаций, доставляющих страдания и неприятные ощущения. Вам не всегда легко вписаться в новое общество, уйти от неприятного общения с неинтересными людьми. Вы проявляете неуверенность в своем вкусе, неумение правильно проследить за своим здоровьем.")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Для Вас характерно неумение найти внутреннее равновесие с самим собой. Избегайте излишней драматизации событий. Разряжайте накопившиеся эмоции в художественном творчестве, в самодеятельности: пении, игре на музыкальных инструментах, театральном искусстве и т.д. Не стремитесь произвести впечатление во что бы то ни стало. От этого могут пострадать Ваши интересы или здоровье. Одевайтесь в соответствии с погодой, соблюдайте последовательность в приеме блюд, отдавайте предпочтение здоровой пище. Старайтесь, чтобы Ваш облик и стиль одежды соответствовали конкретной обстановке. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Не будьте так недоверчивы к комплиментам и оценке Вашей внешности. Не ищите в этом подвоха. Чтобы болезненно не воспринимать повышенный интерес к себе, тренируйтесь работать открыто, на людях. Больше доверяйте людям, не думайте, что они хотят Вам плохого. Еще одна Ваша проблема — это не всегда успешное проявление своих деловых качеств, желание показать себя предприимчивым, активным человеком. Не преувеличивайте значимость своей деловой инициативы. Всегда задавайте себе вопрос, насколько реальны Ваши призывы к действию, могут ли они привести к конкретным положительным результатам. Преодолевайте в себе нежелание считаться с повседневными конкретными делами, в том числе домашними. Конкретнее формулируйте свои проблемы, не будьте навязчивыми по отношению к другим людям. ")]),_c('h4',{attrs:{"align":"left"}},[_vm._v("Ваше общение с людьми может быть затруднено также из-за проявления излишней официальности. Избегайте высокомерия, будьте более демократичны и просты с людьми, не злоупотребляйте иронией и насмешками. Ваша склонность к сарказму может обидеть людей и отвернуть их от Вас.")])])])}
var staticRenderFns = []

export { render, staticRenderFns }